import { Cookies } from 'react-cookie';

const cookies       = new Cookies();
const KEY_TOKEN     = "refresh_token";
const KEY_USERID    = "userId";

/**
 * Refresh Token 저장
 * @param {*} refreshToken 
 * @returns 
 */
export const setRefreshToken = (refreshToken) => {
    const today = new Date();
    const expireDate = today.setDate(today.getDate() + 7);

    return cookies.set(KEY_TOKEN, refreshToken, { 
        sameSite: 'strict', 
        path: "/", 
        expires: new Date(expireDate)
    });
};

/**
 * Refresh Token 반환
 * @returns Refresh Token Key
 */
export const getRefreshToken = () => {
    return cookies.get(KEY_TOKEN);
};

/**
 * Refresh Token 삭제
 */
export const removeRefreshToken = () => {
    return cookies.remove(KEY_TOKEN, { sameSite: 'strict', path: "/" })
}

/**
 * 사용자 ID 저장
 * @param {*} refreshToken 
 * @returns 
 */
export const setUserId = (userId) => {
    const today = new Date();
    const expireDate = today.setDate(today.getDate() + 7);

    return cookies.set(KEY_USERID, userId, { 
        sameSite: 'strict', 
        path: "/", 
        expires: new Date(expireDate)
    });
};

/**
 * 사용자 ID 반환
 * @returns 사용자 ID
 */
export const getUserId = () => {
    return cookies.get(KEY_USERID);
};

/**
 * 사용자 ID 삭제
 * @returns 사용자 ID
 */
export const removeUserId = () => {
    return cookies.remove(KEY_USERID, { sameSite: 'strict', path: "/" })
}