import { Component }    from "react";

import instance         from "service/axiosInstance";
import SearchAddress    from "pages/common/SearchAddress";

import * as CommonUtils from "utils/CommonUtils";

/**
 * Front - Main - 지도 위치 관리 팝업
 */
 export default class  LocationEdit extends Component {
    /**
     * 생성자
     * @param {*} props 
     */
    constructor(props){
        super(props);

        const { detail, fnCallback } = this.props;
        this.state = {
            detail
          , fnCallback
          , postcode    : false // 주소 검색 오픈 여부
        };
    }
        
    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidMount(){
        console.log(`############ ${this.constructor.name}.componentDidMount ############`);
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidUpdate(prevProp, prevState){
        console.log(`############ ${this.constructor.name}.componentDidUpdate ############`);
    }

    /**
     * 렌더링
     * @returns 
     */
    render(){
        const { detail, fnCallback, postcode } = this.state;

        // Func: 닫기 처리
        const hndlClose = () => {
            if(fnCallback){
                fnCallback("edit");
            }
        }
        
        // Func: 주소 검색
        const hndlSelected = data => {
            // console.log(`주소 : ${data.address}, 우편번호: ${data.zonecode}`);
            const addr      = data.address
            const postNum   = data.zonecode;
            const latitude  = data.latitude
            const longitude = data.longitude;
            
            this.setState(prevState => ({ detail:{ ...prevState.detail, addr, postNum, latitude, longitude }, postcode:false}));
        }

        // Func: 내용 변경
        const hndlChange = (key , value) => {
            this.setState(prevState => ({ detail:{ ...prevState.detail, [key]:value }}));
        }

        // Func: 위치 정보 저장
        const fnSave = () => {
            const { detail } = this.state;

            if(CommonUtils.isEmpty(detail.lTitle)){
                alert("지도 이동 위치 명을 입력해주세요.")
            }else if(CommonUtils.isEmpty(detail.postNum) || CommonUtils.isEmpty(detail.addr)){
                alert("주소를 입력해주세요.")
            }else{
                instance.post("/api/main/location/save", detail, {headers: { 'Content-Type': 'application/json;charset=UTF-8' }})
                .then(res => res.data)
                .then(data => {
                    if(data.errMsg !== undefined)   alert(data.errMsg);
                    else                            hndlClose();
                })
                .catch(e => { alert(e); })
            }
        }

        return (
            <div className="layer layer01" style={{display:"block"}}>
                <div className="layer-form">
                    <h5>자동 이동 위치 정보</h5>
                    <ul className="form_cont">
                        <li>
                            <label>
                                <strong>지도 이동 위치 명</strong>
                                <input type="text" name="lTitle" value={detail.lTitle??""} placeholder="지도 이동 위치 명" className="mt10" onChange={e => hndlChange(e.target.name, e.target.value)}/>
                            </label>
                        </li>
                        <li>
                            <label>
                                <strong>우편번호</strong>
                                <input type="text" name="postNum"   value={detail.postNum??""}  readOnly={true}     onFocus={() => this.setState({postcode:true})}  placeholder="우편번호"/>
                                <input type="text" name="addr"      value={detail.addr??""}     readOnly={true}     onFocus={() => this.setState({postcode:true})}  placeholder="주소" className="mt10"/>
                                <input type="text" name="addrDtl"   value={detail.addrDtl??""}  placeholder="상세주소" className="mt10" onChange={e => hndlChange(e.target.name, e.target.value)}/>
                            </label>
                        </li>
                    </ul>
                    <div className="form_button t_c">
                        <button onClick={() => fnSave()} className="button">저장</button>
                    </div>
                </div>
                <button onClick={hndlClose} className="btn_close">레이어 닫기</button>
                {
                    postcode === true?
                    <SearchAddress
                        fnClose     = {() => { this.setState({ postcode:false })}}
                        fnCallback  = {hndlSelected}
                    />
                    :null
                }
            </div>
        )
    }
}