import { Component }    from "react";
import { SortableContainer, SortableElement } from 'react-sortable-hoc';

import instance         from "service/axiosInstance";
import LocationEdit from "./LocationEdit";

/**
 * Front - Main - 위치 목록 팝업
 */
export default class LocationList extends Component {
    /**
     * @description 생성자
     * @param {*} props 
     */
    constructor(props){
        super(props);

        const { fnCallback, onMove } = props;

        this.state = {
            list        : []    // 저장된 지도 이동 위치 목록
          , detail      : null  // 선택된 위치 정보
          , onMove              // 지도 이동 이벤트
          , fnCallback          // 콜백 이벤트
        };
    }
        
    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidMount(){
        // console.log(`############ ${this.constructor.name}.componentDidMount ############`);
        this.fnDelaySearch();
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidUpdate(prevProp, prevState){
        // console.log(`############ ${this.constructor.name}.componentDidUpdate ############`);

        if(prevState.detail !== this.state.detail){
            this.fnSearch();
        }
    }

    /**
     * @description 지연 검색 (트래픽 부하 방지)
     */
    fnDelaySearch = () => {
        if(this.state.timer !== null){
            clearTimeout(this.state.timer);
        }

        const timer = setTimeout(() => { this.fnSearch(); }, 50);
        this.setState({ timer });
    }

    /**
     * @description 검색
     */
     fnSearch = () => {
        instance.get("/api/main/location/list")
        .then(res       => res.data)
        .then(list      => {
            this.setState({ list })
        })
        .catch(error    => {
            if(error.response)      console.error(error.response);
            else if(error.request)  console.log(error.request);
            else                    console.log('Error', error.message);
            console.log(error.config);

            this.setState({ folders:[] });
        })
    }

    /**
     * @description 렌더링
     * @returns 
     */
    render(){
        const { list, detail, onMove, fnCallback } = this.state;

        // Func: 닫기 처리
        const hndlClose = () => {
            if(fnCallback){
                fnCallback("location");
            }
        }
        
        // Func: 콜백처리 (위치 편집)
        const hndlCallback = type => {
            console.log(type);
            if(type === "edit"){
                this.setState({ detail:null });
            }
        }

        // Func: 위치 이동 처리
        const hndlEdit = detail => {
            this.setState({ detail });
        }

        // Func: 위치 이동 처리
        const hndlMove = (latitude, longitude) => {
            if(onMove)  onMove(latitude, longitude);
            hndlClose();
        }

        // Func: 위치 이동 처리
        const hndlFixed = (item) => {
            let lId = null;
            if(item.mainYn !== "Y") lId = item.lId;
            else                    lId = -1;
            
            instance.post("/api/main/location/fix", { lId }, {headers: { 'Content-Type': 'application/json;charset=UTF-8' }})
            .then(res => res.data)
            .then(data => {
                if(data.errMsg !== undefined)   alert(data.errMsg);
                else                            this.fnSearch();
            })
            .catch(e => { alert(e); })
        }
        
        // Element 반환
        return (
            <div id="lyLocation" className="layer layer01" onclick="hideLayout('#lyLocation')" style={{display:"block"}}>
                <div className="layer-location" onclick="stopPropagation()">
                    <h5>지도 이동 위치 목록</h5>
                    <SortableList
                        items       = {list}
                        fnEdit      = {hndlEdit}
                        fnMove      = {hndlMove}
                        fnFixed     = {hndlFixed}
                        onSortEnd   = {this.onSortEnd}
                    />
                    <div className="form_button t_c">
                        <button onClick={() => hndlEdit({})} className="button">+ 위치 정보 추가</button>
                    </div>
                </div>
                <button onClick={hndlClose} className="btn_close">레이어 닫기</button>
                {detail?<LocationEdit detail={detail} fnCallback={hndlCallback} />:null}
            </div>
        )
    }

    onSortEnd = ({oldIndex, newIndex}) => {
        console.log(oldIndex + ", " + newIndex);
        const { list } = this.state;
        const item = list[oldIndex];

        const nList = [];
        for(let idx = 0; idx < list.length; idx++){
            if(oldIndex === idx)    continue;
            if(newIndex === idx)    nList.push(item);
            nList.push(list[idx]);
        }
        // this.setState({ list:nList });
        console.log(nList);
        instance.post("/api/main/location/sort", { list:nList }, {headers: { 'Content-Type': 'application/json;charset=UTF-8' }})
        .then(res => res.data)
        .then(data => {
            if(data.errMsg !== undefined)   alert(data.errMsg);
            else                            this.fnSearch();
        })
        .catch(e => { alert(e); })
    };
}

const SortableItem = SortableElement(({ item, fnEdit, fnMove, fnFixed }) => (
    <li className="loc-item">
        <span className="btn-move">이동</span>
        <strong>{item.lTitle}</strong>
        <button className="btn-edit"    onClick={() => fnEdit(item)}>위치수정</button>
        <button className="btn-trace"   onClick={() => fnMove(item.latitude, item.longitude)}>이동</button>
        <button className={item.mainYn === "Y"?"btn-fix active":"btn-fix"} onClick={() => fnFixed(item)}>고정</button>
    </li>
));

const SortableList = SortableContainer(({ items, fnEdit, fnMove, fnFixed }) => {
    return (
        <ul className="loc-list">
            {items.map((item, index) => (
                <SortableItem key={`loc-${index}`} index={index} item={item} fnEdit={fnEdit} fnMove={fnMove} fnFixed={fnFixed}/>
            ))}
        </ul>
    );
});