import React, { Component } from "react";
import instance             from "service/axiosInstance";

/* Text Editor */
import ReactQuill           from 'react-quill';
import 'react-quill/dist/quill.snow.css';

import CommonUtils      from "utils/CommonUtils";
import BoardComment     from "./BoardComment";

/**
 * 게시글 상세
 */
export default class BoardDetail extends Component {
    /**
     * 생성자
     * @param {*} props 
     */
    constructor(props){
        super(props);

        const { bbsSeq, viewType, onClose } = props;
        this.state = {
            detail      : {}
          , loginUser   : {}
          , bbsSeq      : bbsSeq
          , viewType
          , cmntCont    : ""
          , cmntSeq     : ""
          , timer       : null
          , onClose
        };
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidMount(){
        console.log(`############ ${this.constructor.name}.componentDidMount ############`);

        instance.get("/api/mypage/user")
        .then(res       => res.data && res.data.length > 0?res.data[0]:{})
        .then(data      => this.setState({loginUser:data}))
        .catch(error    => {
            if(error.response)      console.error(error.response);
            else if(error.request)  console.log(error.request);
            else                    console.log('Error', error.message);
            console.log(error.config);
        })
        
        this.fnDelaySearch();
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidUpdate(prevProp, prevState){
        console.log(`############ ${this.constructor.name}.componentDidUpdate ############`);

        const { bbsSeq, viewType } = this.props;
        console.log(`###### ${prevProp.bbsSeq} ${prevProp.bbsSeq} ${prevState.bbsSeq} ####`);
        console.log(`###### ${prevProp.viewType} ${prevProp.bbsSeq} ${prevState.bbsSeq} ####`);
        if(   prevProp.bbsSeq       !== bbsSeq
           || prevState.bbsSeq      !== bbsSeq
        ){
            console.log("????????")
            this.setState({ bbsSeq:bbsSeq,  viewType, detail:{} });
            this.fnDelaySearch();
        }
    }

    /**
     * @description 지연 검색 (트래픽 부하 방지)
     */
    fnDelaySearch = () => {
        if(this.state.timer !== null){
            clearTimeout(this.state.timer);
        }

        const timer = setTimeout(() => { this.fnSearch(); }, 50);
        this.setState({ timer });
    }

    /**
     * 게시글 검색
     */
    fnSearch = () => {
        const { bbsSeq } = this.state;
        if(bbsSeq){
            instance.get("/api/board/detail?bbsSeq=" + bbsSeq)
            .then(res       => res.data && res.data.length > 0?res.data[0]:{})
            .then(detail    => this.setState({ detail, cmntSeq:"", cmntCont:"", timer:null }))
            .catch(e        => this.setState({ detail:null, cmntSeq:"", cmntCont:"", timer:null }))
        }else{
            this.setState({ detail:{}, cmntSeq:"", cmntCont:"", timer:null });
        }
    }

    /**
     * 렌더링
     * @returns 
     */
    render(){
        const { viewType, detail, loginUser, onClose } = this.state;
        
        // Func: 상세 내용 변경
        const setDetail = (key, value) => this.setState(prevState => ({ detail:{...prevState.detail, [key]:value }}));

        // Func: 게시글 저장
        const doSave = () => {
            const { bbsSeq, bbsTitle, bbsCont } = this.state.detail;
            if(CommonUtils.isEmpty(bbsTitle)){
                alert("제목을 입력해주세요.");
                // focusParam("title");
            }else if(CommonUtils.isEmpty(bbsCont)){
                alert("내용을 입력해주세요.");
                // $('#summernote').focus();
            }else{
                instance.post("/api/board/save", { bbsSeq, bbsTitle, bbsCont, bbsType:"B" }, {headers: { 'Content-Type': 'application/json;charset=UTF-8' }})
                .then(res   => res.data)
                .then(data  => {
                    if(data.errMsg !== undefined)               alert(data.errMsg);
                    else {
                        alert("저장되었습니다.")

                        if(this.state.onClose !== undefined){
                            this.state.onClose();
                        }
                    }
                })
                .catch(e => { alert(e); })
            }
        }

        // Func: 게시글 삭제
        const doBbsDel = () => {
            const bbsSeq = detail.bbsSeq;
            if(bbsSeq){
                if(window.confirm("게시글을 삭제하시겠습니까?")){
                    instance.post("/api/board/delete", { bbsSeq }, {headers: { 'Content-Type': 'application/json;charset=UTF-8' }})
                    .then(res => res.data)
                    .then(data => {
                        if(data.errMsg !== undefined)               alert(data.errMsg);
                        else {
                            alert("삭제되었습니다.")
                            if(this.state.onClose !== undefined){
                                this.state.onClose();
                            }
                        }
                    })
                    .catch(e => { alert(e); })
                }
            }
        }

        // Element 반환
        
        if(viewType === "E"){
            // COND: viewType이 EDIT 모드(E)인 경우

            // detail이 선언되지 않은경우 JSON으로 선언
            if(detail === undefined || detail === {})   this.setState({});

            // 화면 출력
            return (
                <div className="bbs-edit">
                    <label>
                        <strong>게시글 제목</strong>
                        <input
                            type        = "text"
                            name        = "bbsTitle"
                            value       = {detail.bbsTitle}
                            placeholder = "게시글 제목"
                            onChange    = {e => { setDetail(e.target.name, e.target.value) }}
                        />
                    </label>
                    <ReactQuill
                        theme       = "snow"
                        value       = {detail.bbsCont}
                        onChange    = {html => { setDetail("bbsCont", html)}}
                    />
                    {/* <div id="summernote">{detail.bbsCont}</div> */}
                    <div className="bbs-button">
                        <button onClick={() => { if(onClose) onClose() }} className="btn-list">목록으로</button>
                        <button onClick={() => doSave()} className="btn-save">저장하기</button>
                    </div>
                </div>
            )
        }else if(Object.keys(detail).length > 0){
            // COND: viewType이 Veiw 모드(V)인 경우
            return(
                <div className="bbs-detail">
                    <h5 className="bbs-title">{detail.bbsType === 'N'?<em className="ic_noti">공지</em>:null}{detail.bbsTitle}</h5>
                    <p className="bbs-info">
                        <strong>{detail.createUserName}</strong>
                        <em>{detail.createDtm}{ /* pattern="yyy.MM.dd HH:mm:ss */ }</em>
                        <em>조회수 : {CommonUtils.fmtNumber(detail.viewCnt)}</em>
                    </p>
                    {
                        (detail.bbsType === 'B' && detail.createUserId === loginUser.userId)?
                        <div className="detail-option">
                            <button onClick={() => { this.setState({ viewType:"E", bbsSeq:detail.bbsSeq }); }} className="btn-edit">수정하기</button>
                            <button onClick={doBbsDel} className="btn-del">삭제하기</button>
                        </div>
                        :(loginUser.userId === 'admin')?
                        <div className="detail-option">
                            <button onClick={doBbsDel} className="btn-del">삭제하기</button>
                        </div>
                        :null
                    }
                    <div className="bbs-cont" dangerouslySetInnerHTML={ {__html: detail.bbsCont} }></div>

                    {/* Comment */}
                    <BoardComment bbsSeq={detail.bbsSeq} />
                </div>
            )
        }else{
            // COND: viewType이 없거나, 보기모드에서 상세 내용이 없는 경우
            return <></>
        }
    }
}