import * as cookie      from 'storage/Cookie';
import * as auth        from 'store/Auth';

import instance         from './axiosInstance';

/**
 * Axios Interceptor
 * Axios 요청에 대한 interceptor
 * JWT 토큰 처리
 */

const interceptor = store => {
    /**
     * 1. 요청 인터셉터 (Request Interceptor)
     * 2개의 콜백 함수를 받음
     */
     instance.interceptors.request.use(
        config => {
            const token = store.getState().authToken.accessToken;
            // if(common.devYn())   console.log(token);
            if(token){
                // config.headers["Authorization"]     = `Bearer ${token}`  // for Spring Boot
                config.headers["x-access-token"]    = token;    // for Node.js Express back-end
            }
            return config;
        }
      , error => {
            // 요청 에러 직전 호출 됨
            return Promise.rejext(error);
        }
    );
    
    /**
     * 2. 응답 인터셉터 (Response Interceptor)
     * 2개의 콜백 함수를 받음
     */
    const { dispatch } = store;
    instance.interceptors.response.use(
        // COND: 2xx 정상 응답 시
        // http status가 200인 경우. 응답 성공 직전 호출 됨. ".then()"으로 이어짐
        res => {
            // const data = res.data;
            // console.log(data);
            return res;
        }
        // COND: 2xx 이외의 오류 응답 시
    , async error => {
            // http status가 200이 아닌 경우. 응답 에러 직전 호출 됨. "chatch()"로 이어짐
            const { config, response: { status } } = error;
            if(status === 401 && !config._retry){
                config._retry = true;

                try {
                    const userId        = cookie.getUserId();
                    const refreshToken  = cookie.getRefreshToken();
                    const res           = await instance.post("/api/common/refresh", { userId, refreshToken });

                    // 새로운 토큰 저장
                    const { accessToken, loginUser } = res;
                    dispatch(auth.SET_TOKEN(accessToken));
                    dispatch(auth.SET_USER(loginUser));

                    // 재 요청
                    return instance(config);
                }catch(err){
                    return Promise.reject(err);
                }
            }

            return Promise.reject(error);
        }
    );
}

export default interceptor;