import { Component }    from "react";

import instance         from "service/axiosInstance";
import CodeService      from "service/CodeService";
import SearchAddress    from "pages/common/SearchAddress";
import CommonUtils      from "utils/CommonUtils";

/**
 * Front - Main - 마커 관리 팝업
 */
export default class MarkerEdit extends Component {
    /**
     * 생성자
     * @param {*} props 
     */
    constructor(props){
        super(props);

        const { item, callback } = props;

        this.state = {
            item                : item??{}
          , callback
          , codHouseType        : []    // 부동산 유형
          , codResidenceType    : []    // 거주 유형
          , codContractCode     : []    // 계약 유형
          , codFacilities       : []    // 중요 옵션
          , cboLoanType         : [{ code:"A", codeName:"안심"}, { code:"C", codeName:"중기청"}, { code:"L", codeName:"LH"}, { code:"S", codeName:"SH"}]    // 대출 유형
          , postcode            : false
          , timer               : null
        };
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidMount(){
        console.log(`############ ${this.constructor.name}.componentDidMount ############`);

        CodeService.getCodeList("P0004").then(list => this.setState({ codHouseType:list }));        // 부동산유형
        CodeService.getCodeList("P0005").then(list => this.setState({ codResidenceType:list }));    // 거주유형
        CodeService.getCodeList("P0001").then(list => this.setState({ codContractCode:list }));     // 계약유형
        CodeService.getCodeList("P0006").then(list => this.setState({ codFacilities:list }));       // 중요옵션
        
        this.fnDelaySearch();
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
    componentDidUpdate(prevProps, prevState){
        console.log(`############ ${this.constructor.name}.componentDidUpdate ############`);

        if(JSON.stringify(prevProps.item) !== JSON.stringify(this.props.item)){
            this.fnDelaySearch();
        }
    }

    /**
     * @description 지연 검색 (트래픽 부하 방지)
     */
     fnDelaySearch = () => {
        if(this.state.timer !== null){
            clearTimeout(this.state.timer);
        }

        const timer = setTimeout(() => { this.fnSearch(); }, 50);
        this.setState({ timer });
    }
    
    /**
     * @description 마커 상세 정보 검색
     */
    fnSearch = () => {
        const { mId } = this.state.item;
        if(mId){
            instance.get("/api/main/marker/detail?mId=" + mId)
            .then(res       => res.data && res.data.length > 0?res.data[0]:{})
            .then(item      => this.setState({ item }))
            .catch(e        => {
                console.log(e);
            });
        }
    }
    
    /**
     * @description 렌더링
     */
    render() {
        const { mId, item, callback, codHouseType, codResidenceType, codContractCode, codFacilities, cboLoanType, postcode } = this.state;

        // Func: 파라미터 세팅
        const hndlParam = (key, value) => { this.setState(prevState => ({item:{ ...prevState.item, [key]:value }})) };

        // Func: 닫기 이벤트 처리
        const hndlClose = () => { if(callback) callback(); }
        
        // Func: 계약 유형 체크
        const hndlCntrtCode = cntrtCode => {
            const codes     = item.cntrtCode?item.cntrtCode.split(","):[];
            const result    = [];
            let already     = false;
            for(let code of codes){
                if(code === cntrtCode){
                    already = true;
                }else{
                    result.push(code);
                }
            }

            if(already === false){
                result.push(cntrtCode);
            }

            result.sort();
            hndlParam("cntrtCode", result.join(","))
        }
        
        // Func: 방 개수 체크
        const hndlCntRoom = type => {
            if(type === "Y"){
                this.setState(prevState => ({ item:{ ...prevState.item, cntRoomChk:"Y"}}));
            }else{
                this.setState(prevState => ({ item:{ ...prevState.item, cntRoomChk:"N", cntRoom:parseInt(type)}}));
            }
        }
        
        // Func: 화장실 개수 체크
        const hndlCntToilet = type => {
            if(type === "Y"){
                this.setState(prevState => ({ item:{ ...prevState.item, cntToiletChk:"Y"}}));
            }else{
                this.setState(prevState => ({ item:{ ...prevState.item, cntToiletChk:"N", cntToilet:parseInt(type)}}));
            }
        }
        
        // Func: 중요 옵션 체크
        const hndlFacilities = facilities => {
            const codes     = item.facilities?item.facilities.split(","):[];
            const result    = [];
            let already     = false;
            for(let code of codes){
                if(code === facilities){
                    already = true;
                }else{
                    result.push(code);
                }
            }

            if(already === false){
                result.push(facilities);
            }

            result.sort();
            hndlParam("facilities", result.join(","))
        }

        // Func: 주소 검색
        const hndlSelected = data => {
            // console.log(`주소 : ${data.address}, 우편번호: ${data.zonecode}`);
            const addr      = data.address
            const postNum   = data.zonecode;
            const latitude  = data.latitude
            const longitude = data.longitude;
            
            this.setState(prevState => ({ item:{ ...prevState.item, addr, postNum, latitude, longitude }, postcode:false}));
        }

        console.log(item);
        return (
            <div className="layer layer01" style={{display:"block"}}>
                <div className="layer-form">
                <ul className="form_cont">
                        <li>
                            <strong>부동산 유형</strong>
                            <span className="cbo-house_type">
                                {codHouseType.map((code, index) => <button key={`houseType${index}`} onClick={() => { hndlParam("houseType", code.code) }} className={code.code === item.houseType?"active":null}><em className={`dot dot0${index + 1}`}></em>{code.codeName}</button>)}
                            </span>
                        </li>
                        <li>
                            <label>
                                <strong>우편번호</strong>
                                <input type="text" name="postNum"   value={item.postNum??""}  readOnly={true}     onFocus={() => this.setState({postcode:true})}  placeholder="우편번호"/>
                                <input type="text" name="addr"      value={item.addr??""}     readOnly={true}     onFocus={() => this.setState({postcode:true})}  placeholder="주소" className="mt10"/>
                                <input type="text" name="addrDtl"   value={item.addrDtl??""}  placeholder="상세주소" className="mt10" onChange={e => hndlParam(e.target.name, e.target.value)}/>
                            </label>
                        </li>
                        <li className="mt10">
                            <strong>건물상태</strong>
                            <span className="cbo-house_type02">
                                <button className={`ic_type01${item.newYn === "N"?" active":" "}`} onClick={() => { hndlParam("newYn", "N") }}>구축</button>
                                <button className={`ic_type02${item.newYn === "Y"?" active":" "}`} onClick={() => { hndlParam("newYn", "Y") }}>신축</button>
                            </span>
                        </li>
                        {
                            item.newYn === "N"?
                            <li>
                                <strong>거주상태 (구축 선택일때)</strong>
                                <span className="cbo-house_type02">
                                    {codResidenceType.map((code, index) => <button key={`residenceType${index}`} onClick={() => { hndlParam("residenceType", code.code) }} className={code.code === item.residenceType?"active":null}>{code.codeName}</button>)}
                                </span>
                            </li>
                            :null
                        }
                        {
                            item.newYn === "Y"?
                            <li><label><strong>오픈예정일자 (신축 선택일때)</strong><input type="text" name="openDt" id="openDt" placeholder="오픈예정일자" readonly="readonly"/></label></li>
                            :null
                        }
                        <li className="mt10">
                            <strong>계약유형</strong>
                            <span className="cbo-house_type02">
                                {codContractCode.map((code, index) => <button key={`cntrtCode${index}`} className={`ic_type0${(index + 3)}${(item.cntrtCode??"").indexOf(code.code) < 0?" ":" active"}`} onClick={() => { hndlCntrtCode(code.code) }}>{code.codeName}</button>)}
                            </span>
                        </li>
                        <li>
                            <div className="div_price">
                                <label>
                                    <strong>가격</strong>
                                    <em><input type="number" name="price" onChange={e => hndlParam(e.target.name, e.target.value)} onFocus={e => e.target.select()}/>만원</em>
                                    {item.price?<span className="txt-kor">{CommonUtils.fmtNumberKor(item.price, 4)}</span>:null}
                                </label>
                                <label>
                                    <strong>월세</strong>
                                    <em><input type="number" name="priceMonthly" onChange={e => hndlParam(e.target.name, e.target.value)} onFocus={e => e.target.select()}/>만원</em>
                                    {item.priceMonthly?<span className="txt-kor">{CommonUtils.fmtNumberKor(item.priceMonthly, 4)}</span>:null}
                                </label>
                            </div>
                        </li>
                        <li className="mt10">
                            <strong>방 개수</strong>
                            <span className="cbo-house_type02">
                                <button key={`cntRoomN`} onClick={() => { hndlCntRoom("Y"); }} className={item.cntRoomChk === "Y"?"active":null}>직접입력</button>
                                { [1, 2, 3, 4, 5].map(count => <button key={`cntRoom${count}`} onClick={() => { hndlCntRoom(count.toString()); }} className={item.cntRoom === count?"active":null}>{count}</button>) }
                                { item.cntRoomChk === "Y"?<input type="number" name="cntRoom" value={item.cntRoom??""} placeholder="방 개수" onKeyDown={e => { CommonUtils.onlyNumber(e, 3) }} onChange={e => hndlParam("cntRoom", e.target.value?parseInt(e.target.value):'')} onFocus={e => e.target.select()}/>:null }
                            </span>
                        </li>
                        <li>
                            <strong>화장실 개수</strong>
                            <span className="cbo-house_type02">
                                <button key={`cboCntToiletN`} onClick={() => { hndlCntToilet("Y"); }} className={item.cntToiletChk === "Y"?"active":null}>직접입력</button>
                                { [1, 2, 3, 4, 5].map(count => <button key={`cntToilet${count}`} onClick={() => { hndlCntToilet(count.toString()); }} className={item.cntToilet === count?"active":null}>{count}</button>) }
                                { item.cntToiletChk === "Y"?<input type="number" name="cntToilet" value={item.cntToilet??""} placeholder="방 개수" onKeyDown={e => { CommonUtils.onlyNumber(e, 3) }} onChange={e => hndlParam("cntToilet", e.target.value?parseInt(e.target.value):'')} onFocus={e => e.target.select()}/>:null }
                            </span>
                        </li>
                        <li className="mt10">
                            <strong>중요 옵션</strong>
                            <span id="cboFacilities" className="cbo-house_type02">
                                {codFacilities.map((code, index) => <button key={`facilities${index}`} className={`${(item.facilities??"").indexOf(code.code) < 0?null:"active"}`} onClick={() => { hndlFacilities(code.code) }}>{code.codeName}</button>)}
                            </span>
                        </li>
                        <li>
                            <div className="div_price">
                                <label>
                                    <strong>전용면적</strong>
                                    <em><input type="number" name="areaPrivate" value={item.areaPrivate??""} onKeyDown={e => { CommonUtils.onlyNumber(e, 5) }} onChange={e => hndlParam("areaPrivate", e.target.value?parseInt(e.target.value):'')} onFocus={e => e.target.select()}/>평</em>
                                </label>
                                <label>
                                    <strong>연식</strong>
                                    <em><input type="number" name="areaSupply" value={item.areaSupply??""} onKeyDown={e => { CommonUtils.onlyNumber(e, 5) }} onChange={e => hndlParam("areaSupply", e.target.value?parseInt(e.target.value):'')} onFocus={e => e.target.select()}/>년</em>
                                </label>
                            </div>
                        </li>
                        <li className="mt10">
                            <strong>대출유형</strong>
                            <span id="cboLoanType" className="cbo-house_type02">
                                { cboLoanType.map((code, index) => <button key={`loanType${index}`} onClick={() => { hndlParam("loanType", code.code); }} className={item.loanType === code.code?"active":null}>{code.codeName}</button>) }
                            </span>
                        </li>
                        <li className="div_tel">
                            <label>
                                <strong>전화번호</strong>
                                <span>
                                    <input type="text" name="phoneNumber"   placeholder="전화번호01"    value={item.phoneNumber}    onChange={e => { hndlParam(e.target.name, e.target.value) }} />
                                    <input type="text" name="phoneNumber02" placeholder="전화번호02"    value={item.phoneNumber02}  onChange={e => { hndlParam(e.target.name, e.target.value) }} />
                                </span>
                            </label>
                        </li>
                        <li>
                            <div className="div_price">
                                <label>
                                    <strong>리베이트</strong>
                                    <em>매매<input type="number" name="rebateTrade" value={item.rebateTrade??""} onKeyDown={e => { CommonUtils.onlyNumber(e, 3) }} onChange={e => hndlParam("rebateTrade", e.target.value?parseInt(e.target.value):'')} onFocus={e => e.target.select()}/></em>
                                </label>
                                <label>
                                    <strong>&nbsp;</strong>
                                    <em>전세<input type="number" name="rebateRent" value={item.rebateRent??""} onKeyDown={e => { CommonUtils.onlyNumber(e, 3) }} onChange={e => hndlParam("rebateRent", e.target.value?parseInt(e.target.value):'')} onFocus={e => e.target.select()}/></em>
                                </label>
                            </div>
                        </li>
                        <li className="mt10">
                            <strong>사진</strong>
                            <dl className="list-photo">
                                {[1, 2, 3, 4, 5, 6].map(seq => 
                                    <dd><img alt={`imgUrl${seq}`} src="/images/common/ic_photo01.png" onError={e => e.target.src = '/images/common/ic_photo01.png'}/><button onclick="_image.doRemoveImage('imgUrl0${s1.count}');return false;" className="btn_del">삭제</button></dd>
                                )}
                            </dl>
                        </li>
                        <li><label><strong>상세설명</strong><textarea name="mDesc" value={item.mDesc} onChange={e => hndlParam(e.target.name, e.target.value)}></textarea></label></li>
                    </ul>
                    {item.modifyDtmTxt?
                    <p id="txtMarkerLastModifyDt" style={{fontSize:"12px", padding:"0 10px", color:"#6c6c6c"}}>최종 수정일시 : {item.modifyDtmTxt}</p>
                    :null
                    }
                    <div className="form_button">
                        <button onclick="_marker.deleteMarkerForm();return false;" className="button type03">삭제</button>
                        <button onclick="_marker.saveMarkerForm();return false;" className="button">저장</button>
                    </div>
                </div>
                <button onClick={hndlClose} className="btn_close">레이어 닫기</button>
                {
                    postcode === true?
                    <SearchAddress
                        fnClose     = {() => { this.setState({ postcode:false })}}
                        fnCallback  = {hndlSelected}
                    />
                    :null
                }
            </div>
        )
    }
}