/**
 * Front - Main - 공유하기 팝업
 */
function LayerShare(props) {
    // Func: 레이어 닫기
    const closeLayer = e => {
        e.preventDefault();
        props.callback("share");
    }

    {/* 제거: 레이어 클릭 시 닫힘 onClick={closeLayer} */}
    return (
        <div id="lyShare" className="layer layer01" style={{ display:"flex"}}>
            <div onclick="stopPropagation()" className="layer-share">
                <h5>공유하기<a href="#" onclick="hideLayout('#lyShare');return false;" className="btn_close">레이어 닫기</a></h5>
                <div className="share-list">
                    <a href="#" className="kakao kakao-link" onclick="_shareKakao();return false;">카톡공유</a>
                    <a href="#" className="sms" onclick="_shareSMS();return false;">문자공유</a>
                </div>
            </div>
        </div>
    )
}

export default LayerShare;