import { Component }    from "react";
import instance         from "service/axiosInstance";

import BoardItem        from "./BoardItem";
import Pager            from "pages/common/Pager";

/**
 * 게시글 목록
 */
export default class BoardList extends Component {
    /**
     * 생성자
     * @param {*} props 
     */
    constructor(props){
        super(props);

        const { onClick } = props;
        this.state = {
            notices     : []    // 공지사항
          , list        : []    // 게시글 목록
          , page        : 1     // 게시글 페이지
          , totalCount  : 0     // 게시글 전체 개수
          , bbsSeq      : ""
          , onClick     : onClick
          , timer       : null
        };
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidMount(){
        console.log(`############ ${this.constructor.name}.componentDidMount ############`);
        this.fnDelaySearch();
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidUpdate(prevProp, prevState){
        console.log(`############ ${this.constructor.name}.componentDidUpdate ############`);

        const { bbsSeq } = this.props;
        const { page }   = this.state;
        if(
            prevProp.bbsSeq     !== bbsSeq
         || prevState.page      !== page
        ){
            this.setState({ bbsSeq:bbsSeq??"", page:page });
            this.fnDelaySearch();
        }

        // Step. 게시글 최상단으로 이동
        window.scrollTo(0, 0);
    }

    /**
     * @description 지연 검색 (트래픽 부하 방지)
     */
    fnDelaySearch = () => {
        if(this.state.timer !== null){
            clearTimeout(this.state.timer);
        }

        const timer = setTimeout(() => { this.fnSearch(); }, 50);
        this.setState({ timer });
    }

    /**
     * @description 검색
     */
     fnSearch = () => {
        const { page } = this.state;

        instance.get(`/api/board/list?page=${page}`)
        .then(res => res.data)
        .then(data => this.setState({ notices:data[0], list:data[1], totalCount:data[2][0].totalCount, timer:null }))
        .catch(error => {
            if(error.response)      console.error(error.response);
            else if(error.request)  console.log(error.request);
            else                    console.log('Error', error.message);
            console.log(error.config);

            this.setState({ notices:[], list:[], timer:null });
        })
    }

    /**
     * 렌더링
     * @returns 
     */
    render(){
        const { notices, list, totalCount, page, bbsSeq, onClick } = this.state;

        // Func: 게시글 클릭 핸들러
        const fnClick = (type, seq) => {
            if(onClick !== undefined){
                if(seq === bbsSeq)    seq = "";
                onClick(type, seq)
            }
        }

        // 페이징 처리
        const hndlPage = (nPage) => this.setState({ page:nPage })

        // Element 반환
        return(
            <div className="bbs-list">
                <div className="list-option">
                    <button onClick={() => {fnClick("E", "")}} className="btn-edit">게시글쓰기</button>
                </div>
                <ul>
                    {notices.map((item, index) => <BoardItem key={`notice${index}`} name={"notice"} item={item} onClick={fnClick} bbsSeq={bbsSeq}/>)}
                    {list.map((item, index) => <BoardItem key={`board${index}`} name={"board"} item={item} onClick={fnClick} bbsSeq={bbsSeq}/>)}
                </ul>
                <Pager listSize={5} page={page} totalCount={totalCount} onClick={hndlPage}/>
            </div>
        )
    }
}