import { Component }    from "react";

import CommonUtils      from "utils/CommonUtils";

/**
 * 게시글 목록 - 개별 아이템
 */
export default class BoardItem extends Component {
    /**
     * 생성자
     * @param {*} props 
     */
    constructor(props){
        super(props);

        const { name, item, onClick, bbsSeq } = props;
        this.state = { name, item, onClick, bbsSeq };
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
    componentDidMount(){
        console.log(`############ ${this.constructor.name}.componentDidMount ############`);
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
    componentDidUpdate(prevProp, prevState){
        console.log(`############ ${this.constructor.name}.componentDidUpdate ############`);
        if(JSON.stringify(prevProp) !== JSON.stringify(this.props)){
            const { name, item, onClick, bbsSeq } = this.props;
            this.setState({ name, item, onClick, bbsSeq });
        }
    }

    /**
     * 렌더링
     * @returns 
     */
    render(){
        const { name, item, onClick, bbsSeq } = this.state;
        
        // Func: 게시글 클릭 핸들러
        const fnClick = (viewType, bbsSeq) => {
            if(onClick !== undefined){
                onClick(viewType, bbsSeq)
            }
        }
        
        // Element 반환
        return(
            <li key={name + item.bbsSeq} onClick={() => {fnClick("V", item.bbsSeq)}} className={(bbsSeq === item.bbsSeq)?"active":""}>
                <span className="bbs-title">
                    {item.bbsType === 'N'?<em className="ic_noti">공지</em>:null}
                    {item.bbsTitle}
                    {item.cmntCnt > 0?` [${CommonUtils.fmtNumber(item.cmntCnt)}]`:""}
                    {item.dateDiff < 3?<em className="ic_new">N</em>:null}
                </span>
                <span className="bbs-info">
                    <strong>{item.createUserName}</strong>
                    <em>{item.createDtm}</em>
                    <em>조회수 : {CommonUtils.fmtNumber(item.viewCnt)}</em>
                </span>
            </li>
        )
    }
}