import { Component }    from "react"

import Drawer2Depth     from "./Drawer2Depth";

/**
 * Front - Drawer - 1뎁스 (시/도)
 */
export default class Drawer1Depth extends Component {
    /**
     * @deracription 생성자
     * @param {*} props 
     */
    constructor(props){
        super(props);

        const { data, onClick } = props;
        this.state = {
            data
          , onClick
          , unfolded    : []  // 하위 뎁스가 열려 있는 ITEM
        };
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidMount(){
        // console.log(`############ ${this.constructor.name}.componentDidMount ############`);
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
    componentDidUpdate(prevProp, prevState){
        // console.log(`############ ${this.constructor.name}.componentDidUpdate ############`);
        if(JSON.stringify(prevProp) !== JSON.stringify(this.props)){
            const { data, onClick } = this.props;
            this.setState({ data, onClick });
        }
    }

    /**
     * @description 렌더링
     * @returns 
     */
    render(){
        const { data, onClick, unfolded } = this.state;
    
        // Func: Drawer folded / unfolded
        const hndlFold  = key => {
            if(this.state.unfolded.indexOf(key) < 0){
                this.state.unfolded.push(key);
            }else{
                this.state.unfolded.pop(key);
            }
            this.setState({ unfolded:this.state.unfolded })
        };
    
        // Func: 아이템 클릭 핸들러
        const hndlClick = item => { if(onClick) { onClick(item); } };

        // Element 반환
        if(Object.keys(data).length > 0){
            return (
                Object.keys(data).sort().map(key => {
                    const item      = data[key];
                    const folded    = unfolded.indexOf(key) < 0?true:false;
                    // const clsName  = ["btn-folder", "folded"];
                    const clsName  = ["btn-folder"];
                    if(folded === true)         clsName.push("folded");
                    if(item.checked === "Y")    clsName.push("active");
                    
                    return (
                        <li key={key} className={clsName.join(" ")}>
                            <strong onClick={() => hndlClick(item)}>{item.name} <em>({item.count})</em></strong>
                            {
                                (item.name !== '미분류' && Object.keys(item.child).length > 0)?
                                <>
                                    <button className={folded === true?"btn_fold":"btn_fold active"} onClick={() => hndlFold(key)} title="폴더접기/펼치기">폴더접기/펼치기</button>
                                    <Drawer2Depth data={item.child} onClick={onClick} />
                                </>
                                :null
                            }
                        </li>
                    );
                })
            )
        }else{
            return <></>;
        }
    }
}