import { Component }    from "react";

import CommonUtils      from "utils/CommonUtils";

const CNTRT_CODE = {
    "C0101" : { text:"매", className:"m01" }
  , "C0102" : { text:"전", className:"m02" }
  , "C0104" : { text:"월", className:"m04" }
  , "C0103" : { text:"기", className:"m03" }
};


/**
 * 검색 결과 ITEM
 */
export default class SearchItem extends Component {
    /**
     * @description 생성자
     * @param {*} props 
     */
    constructor(props){
        super(props);

        const { list, keyword, fnCallback } = props;

        this.state = { list, keyword, fnCallback, checked:[] };
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidMount(){
        // console.log(`############ ${this.constructor.name}.componentDidMount ############`);
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
    componentDidUpdate(prevProps, prevState){
        // console.log(`############ ${this.constructor.name}.componentDidUpdate ############`);
        if(JSON.stringify(prevProps) !== JSON.stringify(this.props)){
            const { list, keyword, fnCallback } = this.props;
            this.setState({ list, keyword, fnCallback, checked:[] });
        }
    }

    /**
     * @description 렌더링
     * @returns 
     */
    render() {
        const { list, keyword, fnCallback, checked } = this.state;

        // Func: 콜백
        const hndlCallback = (type, item) => {
            if(fnCallback)  fnCallback(type, item);
        }

        // Func: 체크박스 처리
        const hndlCheck = mId => {
            const result    = [];
            let already     = false;
            for(let check of checked){
                if(check === mId)   already = true;
                else                result.push(check);
            }
            if(already === false)   result.push(mId);
            result.sort();
            this.setState({ checked:result });
        }

        // Func: 텍스트에서 검색 키워트 체크
        const fnCheckKeyword = value => {
            if(CommonUtils.isEmpty(value) === true)    return "";
            
            const keywords  = keyword.split(",");
            const result    = [];
            for(let kIdx = 0; kIdx < keywords.length; kIdx++){
                const val = keywords[kIdx];
                
                if(CommonUtils.isEmpty(val) === true)   continue;
                
                const arr = value.split(val)
                for(let sIdx = 0; sIdx < arr.length; sIdx++){
                    if(sIdx > 0)    result.push(<em key={`${value}_${sIdx}`} className='bc_yellow01'>{val}</em>);
                    result.push(arr[sIdx]);
                }
            }
            
            return result;
        }

        // Step. Element 반환
        return (
            <div className="search-list">
                {
                list.length > 0?
                list.map((item, index) =>
                    <div key={`searchItem${index}`} className="search-item">
                        <div key={`searchItem${index}01`} className="item-info" onClick={() => { hndlCallback("show", item)}}>
                            <em className="num">{index + 1}</em>
                            <strong className="ic_pin">
                                {fnCheckKeyword(item.addrDtl)}
                                {item.newYn === 'Y'?<span className="ic_new-yn">신축</span>:null}
                                {CommonUtils.isEmpty(item.cntrtCode) === false?
                                item.cntrtCode.split(",").map((cntrt, cIdx) =>
                                    <u key={`searchItem${index}_cntrt${cIdx}`} className={CNTRT_CODE[cntrt].className}>{CNTRT_CODE[cntrt].text}</u>)
                                :null
                                }
                                <i className={`price ${item.houseType}`}>{CommonUtils.fmtCurrency(item.price, 4)}</i>
                                {item.priceMonthly > 0?<i className={`price ${item.houseType}`}>{CommonUtils.fmtCurrency(item.priceMonthly, 4)}</i>:null}
                            </strong>
                            <span>최종수정일시 : {item.modifyDtmTxt}</span>
                            <span>{fnCheckKeyword(`매물번호 #${item.mId} (등록자: ${item.createUserName})`)}</span>
                            <span>
                                {fnCheckKeyword(item.phoneNumber)}
                                {CommonUtils.isEmpty(item.phoneNumber02) === false?(" / " + fnCheckKeyword(item.phoneNumber02)):""}
                            </span>
                            <span>
                                {CommonUtils.isEmpty(item.poseNum)  === false?`[${item.poseNum}]`:null}
                                {CommonUtils.isEmpty(item.addr)     === false?item.addr:null}
                                {CommonUtils.isEmpty(item.addrDtl)  === false?item.addrDtl:null}
                            </span>
                            <span>{item.mDesc}</span>
                        </div>
                        <div key={`searchItem${index}02`} className="item-control">
                            <button className={`ic_check ${checked.indexOf(item.mId) >= 0?"checked":""}`} onClick={() => { hndlCheck(item.mId)}}>선택</button>
                            <button className="btn_edit" onClick={() => { hndlCallback("edit", item)}}>수정</button>
                        </div>
                    </div>
                )
                :<p className="nodata">키워드를 입력해주세요.</p>
                }
                
            </div>
        )
    }
}