import React, { Component } from "react";

import instance             from "service/axiosInstance";
import Drawer1Depth         from "./Drawer1Depth";

const depthKey = "key"; // Drawer 키 Prefix

/**
 * Front - 매물 지도 - Drawer
 */
export default class Drawer extends Component {
    /**
     * @description 생성자
     * @param {*} props 
     */
    constructor(props){
        super(props);

        const { visible, onClose } = props;
        this.state = {
            visible         // 보이기 여부
          , onClose         // 닫기 이벤트
          , list    : []    // 메뉴 목록
          , tree    : {}    // 메뉴 트리
        };

        this.drawer = React.createRef();
    }
    
    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidMount(){
        // console.log(`############ ${this.constructor.name}.componentDidMount ############`);
        this.fnDelaySearch();
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidUpdate(prevProp, prevState){
        // console.log(`############ ${this.constructor.name}.componentDidUpdate ############`);

        if(prevProp.visible !== this.props.visible){
            this.setState({ visible:this.props.visible });
        }
    }

    /**
     * @description 지연 검색 (트래픽 부하 방지)
     */
    fnDelaySearch = () => {
        if(this.state.timer !== null){
            clearTimeout(this.state.timer);
        }

        const timer = setTimeout(() => { this.fnSearch(); }, 50);
        this.setState({ timer });
    }

    /**
     * @description 검색
     */
     fnSearch = () => {
        instance.get("/api/main/folder/list")
        .then(res       => res.data)
        .then(list      => {
            // Step. 메뉴 트리 만들기
            const tree = {};
            for(let rowIdx = 0; rowIdx < list.length; rowIdx++){
                const item            = list[rowIdx];

                const { 
                    depth1Seq, depth1Name, depth1Checked, depth1Cnt
                  , depth2Seq, depth2Name, depth2Checked, depth2Cnt
                  , depth3Seq, depth3Name, depth3Checked, depth3Cnt
                }   = item;
                const depth1Key       = `${depthKey}${depth1Seq}`;
                const depth2Key       = `${depthKey}${depth2Seq}`;
                const depth3Key       = `${depthKey}${depth3Seq}`;
                
                // Step. 1 뎁스 (시/도) 생성
                if(tree[depth1Key] === undefined){
                    // Element 생성
                    const clsNames = ["btn-folder", "folded"];
                    if(depth1Checked == "Y")      clsNames.push("active");
                    
                    const sortNo    = Object.keys(tree).length + 1;
                    tree[depth1Key] = { depth:[depth1Seq], name:depth1Name, checked:depth1Checked, count:depth1Cnt, child:{}, sortNo };
                }
                
                // 미분류가 아닌 경우에만 다음 뎁스 생성
                // Step. 2 뎁스 (시/군/구) 생성
                if(tree[depth1Key].child[depth2Key] === undefined){
                    const sortNo    = Object.keys(tree[depth1Key]).length + 1;
                    tree[depth1Key].child[depth2Key] = { depth:[depth1Seq, depth2Seq], name:depth2Name, checked:depth2Checked, count:depth2Cnt, child:{}, sortNo };
                }
                
                // Step. 3 뎁스 (읍/면/동) 생성
                if(tree[depth1Key].child[depth2Key].child[depth3Key] === undefined){
                    const sortNo    = Object.keys(tree[depth1Key].child[depth2Key]).length + 1;
                    tree[depth1Key].child[depth2Key].child[depth3Key] = { depth:[depth1Seq, depth2Seq, depth3Seq], name:depth3Name, checked:depth3Checked, count:depth3Cnt, child:{}, sortNo };
                }
            }

            this.setState({ list, tree })
        })
        .catch(error    => {
            if(error.response)      console.error(error.response);
            else if(error.request)  console.log(error.request);
            else                    console.log('Error', error.message);
            console.log(error.config);

            this.setState({ folders:[] });
        })
    }

    /**
     * @description 저장하기
     */
     fnSave = (tree) => {
        const checked   = [];

        // Step. 1뎁스 체크
        for(let key in tree){
            const item = tree[key];
            
            // Step. 2뎁스 체크
            for(let key2 in item.child){
                const item2 = item.child[key2];

                // Step. 3뎁스 체크
                for(let key3 in item2.child){
                    const item3 = item2.child[key3];
                    if(item3.checked === "Y"){
                        checked.push(item.name + " " + item2.name + " " + item3.name);
                    }
                }
            }
        }

        instance.post("/api/main/folder/save", { checked:checked.join(",") }, {headers: { 'Content-Type': 'application/json;charset=UTF-8' }})
        .then(res => res.data)
        .then(data => {
            if(data.errMsg !== undefined)   alert(data.errMsg);
            else                            this.fnSearch();
        })
        .catch(e => { alert(e); })
    }

    /**
     * @description 렌더링
     * @returns 
     */
    render() {
        const { visible, onClose, tree } = this.state;

        // Func: Drawer 닫기 처리
        const hndlClose = () => { if(onClose) onClose(); }
        
        const hndlClick = (item) => {
            const depth = item?item.depth.length:0;
            if(depth === 0){
                // 전체 체크 여부
                let wholechecked = true;
                for(let key in tree){
                    if(tree[key].checked !== "Y"){
                        wholechecked = false;
                        break;
                    }
                }
                
                // Step. 1뎁스 체크 변경
                for(let key in tree){
                    tree[key].checked = wholechecked == true?"N":"Y";

                    // Step. 2뎁스 체크 변경
                    for(let key2 in tree[key].child){
                        tree[key].child[key2].checked = tree[key].checked;

                        // Step. 3뎁스 체크 변경
                        for(let key3 in tree[key].child[key2].child){
                            tree[key].child[key2].child[key3].checked = tree[key].child[key2].checked;
                        }
                    }
                }
            } else if(depth === 1) {
                // COND: 1뎁스 클릭 시
                const depth1Key       = `${depthKey}${item.depth[0]}`;
                const checked       = item.checked;

                // Step. 1뎁스 체크 변경
                if(checked === "Y") tree[depth1Key].checked = "N";
                else                tree[depth1Key].checked = "Y";

                // Step. 2뎁스 체크 변경
                for(let key in tree[depth1Key].child){
                    tree[depth1Key].child[key].checked = tree[depth1Key].checked;

                    // Step. 3뎁스 체크 변경
                    for(let key2 in tree[depth1Key].child[key].child){
                        tree[depth1Key].child[key].child[key2].checked = tree[depth1Key].child[key].checked;
                    }
                }
            } else if(depth === 2) {
                // COND: 2뎁스 클릭 시
                const depth1Key     = `${depthKey}${item.depth[0]}`;
                const depth2Key     = `${depthKey}${item.depth[1]}`;
                const checked       = item.checked;

                // Step. 2뎁스 체크 변경
                if(checked === "Y") tree[depth1Key].child[depth2Key].checked = "N";
                else                tree[depth1Key].child[depth2Key].checked = "Y";

                // Step. 3뎁스 체크 변경
                for(let key in tree[depth1Key].child[depth2Key].child){
                    tree[depth1Key].child[depth2Key].child[key].checked
                    = tree[depth1Key].child[depth2Key].checked;
                }

                // Step. 1뎁스 내 체크 확인
                let checked1Depth = true;
                for(let key in tree[depth1Key].child){
                    if(tree[depth1Key].child[key].checked !== "Y"){
                        checked1Depth = false;
                        break;
                    }
                }
                tree[depth1Key].checked = (checked1Depth === true?"Y":"N");
            } else if(depth === 3) {
                // COND: 3뎁스 클릭 시
                const depth1Key     = `${depthKey}${item.depth[0]}`;
                const depth2Key     = `${depthKey}${item.depth[1]}`;
                const depth3Key     = `${depthKey}${item.depth[2]}`;
                const checked       = item.checked;

                // Step. 3뎁스 체크 변경
                if(checked === "Y") tree[depth1Key].child[depth2Key].child[depth3Key].checked = "N";
                else                tree[depth1Key].child[depth2Key].child[depth3Key].checked = "Y";

                // Step. 2뎁스 내 체크 확인
                let checked2Depth = true;
                for(let key in tree[depth1Key].child[depth2Key].child){
                    if(tree[depth1Key].child[depth2Key].child[key].checked !== "Y"){
                        checked2Depth = false;
                        break;
                    }
                }
                tree[depth1Key].child[depth2Key].checked = (checked2Depth === true?"Y":"N");

                // Step. 1뎁스 내 체크 확인
                let checked1Depth = true;
                for(let key in tree[depth1Key].child){
                    if(tree[depth1Key].child[key].checked !== "Y"){
                        checked1Depth = false;
                        break;
                    }
                }
                tree[depth1Key].checked = (checked1Depth === true?"Y":"N");
            }
            
            this.fnSave(tree);
            // this.setState({ tree });
        }

        // 전체 체크 여부
        let wholechecked = true;
        for(let key in tree){
            if(tree[key].checked !== "Y"){
                wholechecked = false;
                break;
            }
        }

        // Element 반환
        return (
            <div className={visible === true?"layer-navi active":"layer-navi"}>
                <div className="layer-menu">
                    <ul className="menu-list">
                        <li key="menuWhole" className={wholechecked === true?"btn-folder active":"btn-folder"}><strong onClick={() => hndlClick()}>지역 전체 선택/해제</strong></li>
                        <Drawer1Depth ref={this.drawer} data={tree} onClick={hndlClick} />
                    </ul>
                </div>
                <button onClick={hndlClose} className="btn_close">레이어 닫기</button>
            </div>
        )
    }
}