import { Component }        from "react"
import { Link }             from "react-router-dom";

import instance             from "service/axiosInstance";
import CommonUtils          from "utils/CommonUtils";

import Footer               from "pages/front/menu/Footer";

const IMAGE_SERVER = process.env.REACT_APP_IMAGE_SERVER;

/**
 * @path    Front - My Page
 * @url     /friend
 */
export default class Mypage extends Component {
    constructor(props){
        super(props);

        this.state = {
            user        : {}    // 로그인 사용자 정보
          , userPwd     : ""    // 신규 비밀번호
          , userPwdCnfm : ""    // 신규 비밀번호 확인
        }
    }

    /**
     * 검색
     */
    fnSearch = () => {
        instance.get("/api/mypage/user")
        .then(res => res.data && res.data.length > 0?res.data[0]:{})
        .then(data => {
            this.setState({user:data});
        })
        .catch(error => {
            if(error.response === 'jwt must be provided'){
                this.fnSearch();
            }

            if(error.response){
                console.error(error.response);
            }else if(error.request){
                console.log(error.request);
            }else{
                console.log('Error', error.message);
            }
            console.log(error.config);
            // window.location.replace("/");
            // this.props.navigation.navigate("/");
        })
    }

    componentDidMount(){
        this.fnSearch();
    }

    render() {
        const loginUser = this.state.user;   // 현재 로그인 계정

        // Func: 사용자 정보 세팅
        const setUserInfo = (key, value) => {
            this.setState(prevState => ({ user:{...prevState.user, [key]:value }}))
        }

        // Func: 프로필 첨부 하기
        const attachProfile = () => {
            // TODO: 서버 업로드 후 url 반환 로직 추가
        }

        // Func: 프로필 첨부 삭제
        const deleteProfile = () => {
            setUserInfo("profile", null);
        }

        // Func: 첨부 이미지 첨부 하기
        const attachImage = () => {
            // TODO: 서버 업로드 후 url 반환 로직 추가
        }

        // Func: 첨부 이미지 삭제
        const deleteImage = () => {
            setUserInfo("imgUrl", null);
        }

        // 비밀번호 저장
        const doSavePwd = () => {
            if(window.confirm("비밀번호를 변경하시겠습니까?")){
                const { userPwd, userPwdCnfm } = this.state;
                if(CommonUtils.isEmpty(userPwd)){
                    alert("비밀번호를 입력해주세요.");
                }else if(userPwd.split(" ").length > 1){
                    alert("비밀번호에 공란은 입력할수 없습니다");
                }else if(userPwd !== userPwdCnfm){
                    alert("비밀번호와 비밀번호 확인이 일치하지 않습니다.");
                }else{
                    instance.post("/api/mypage/password", { userPwd, userPwdCnfm }, {headers: { 'Content-Type': 'application/json;charset=UTF-8' }})
                    .then(res => res.data)
                    .then(data => {
                        if(data.errMsg !== undefined)    alert(data.errMsg);
                        else{
                            alert(data.message);
                            this.setState({userPwd:""})
                            this.setState({userPwdCnfm:""})
                        }
                    })
                    .catch(e => { alert(e); })
                }
            }
        }

        return (
            <>
                <div id="my_wrap">
                    <div id="my_header">
                        <h4>MapMarker</h4>
                        <Link to="/" className="btn_back">공유지도</Link>
                    </div>
                    <div id="my_contents">
                        <p className="my_id">{loginUser.userId}</p>
                        <div className="form-edit">
                            <ul>
                                <li id="profile" className="profile-box">
                                    <button onClick={attachProfile} className="profile" style={{backgroundImage:loginUser.profile?`url('${IMAGE_SERVER + loginUser.profile}')`:"", backgroundSize:"contain"}}>프로필 이미지</button>
                                    {
                                        loginUser.profile
                                        ?<button onClick={deleteProfile} className="btn-del">삭제하기</button>
                                        :null
                                    }
                                </li>
                                <li>
                                    <input
                                        type        = "text"
                                        name        = "userName"
                                        value       = {loginUser.userName}
                                        placeholder = "사용자 명"
                                        onChange    = {e => { setUserInfo(e.target.name, e.target.value) }}
                                    />
                                </li>
                                <li>
                                    <div className="photo">
                                        <p id="imgUrl">{loginUser.imgUrl?<img alt="myPhoto" src={IMAGE_SERVER + loginUser.imgUrl} style={{maxWidth:"200px", maxHeight:"100px", marginBottom:"10px"}}/>:null}</p>
                                        {!loginUser.imgUrl?<button className="button" onClick={attachImage}>이미지 첨부</button>:null}
                                        {loginUser.imgUrl?<button className="button type03" onClick={deleteImage}>이미지 삭제</button>:null}
                                        <p className="mt10">컨설&amp;분양팀&amp;부동산은 매물을 공유할 수 있는 '공유지도' 메뉴를 사용할 수 있습니다. 명함 사진을 등록해 주세요.</p>
                                    </div>
                                </li>
                            </ul>
                            <div className="form-button">
                                <button onClick={e => { /* doSaveName() */ }} className="btn-save">저장하기</button>
                            </div>
                        </div>
                        { /* 비밀번호 변경 */ }
                        <div className="form-edit">
                            <ul>
                                <li>
                                    <input
                                        type        = "password"
                                        value       = {this.state.userPwd}
                                        name        = "userPwd"
                                        placeholder = "비밀번호"
                                        onChange    = {e => this.setState({userPwd:e.target.value})}
                                    />
                                </li>
                                <li>
                                    <input
                                        type        = "password"
                                        value       = {this.state.userPwdCnfm}
                                        name        = "userPwdCnfm"
                                        placeholder = "비밀번호 확인"
                                        onChange    = {e => this.setState({userPwdCnfm:e.target.value})}
                                    />
                                </li>
                            </ul>
                            <div className="form-button">
                                <button onClick={doSavePwd} className="btn-save">저장하기</button>
                            </div>
                        </div>
                        { /* 비밀번호 변경 */ }
                        { /* 프리미엄 정보 */ }
                        {
                            loginUser.primeExpiredYn !== "Y"?
                            <div className="prime-card">
                                <p><strong>프라임회원</strong></p>
                                <p><span>{loginUser.primeDueMonth} 개월 ({loginUser.primeStartDt} ~ {loginUser.primeExpiredDt})</span></p>
                            </div>
                            :null
                        }
                        { /* 프리미엄 정보 */ }
                        <Link to="/logout" className="btn-logout">로그아웃</Link>
                    </div>
                </div>
                <Footer/>
            </>
        )
    }
}