import { Component }    from "react"

import CommonUtils      from "utils/CommonUtils";

/**
 * @description 체크박스 조건
 */
export default class CondCheck extends Component {
    /**
     * @description 생성자
     * @param {*} props 
     */
    constructor(props){
        super(props);

        const { title, type, clsName, dot, name, list, cond, fnCallback } = props;
        this.state = { title, type, clsName, dot, name, list, cond, fnCallback };
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidMount(){
        // console.log(`############ ${this.constructor.name}.componentDidMount ############`);
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
    componentDidUpdate(prevProps, prevState){
        // console.log(`############ ${this.constructor.name}.componentDidUpdate ############`);
        if(JSON.stringify(prevProps) !== JSON.stringify(this.props)){
            const { title, type, clsName, dot, name, list, cond, fnCallback } = this.props;
            this.setState({ title, type, clsName, dot, name, list, cond, fnCallback });
        }
    }
    
    /**
     * @description 렌더링
     * @returns 
     */
    render() {
        // Step. 상태 값
        const { title, type, clsName, dot, name, list, cond, fnCallback } = this.state;

        // Func: 클릭 이벤트 처리
        const hndlCheck = code => { if(fnCallback) fnCallback(name, code); };

        // Step. Element 반환
        return (
            <div className="mt10">
                <strong>{title}</strong>
                <span className={type}>
                    {list.map((item, index) => {
                        const cls       = clsName?clsName.replace("#seq", CommonUtils.lpad(index + 1, 2, '0')):null;
                        const em        = dot?<em className={dot.replace("#seq", CommonUtils.lpad(index + 1, 2, '0'))}></em>:null;
                            
                        const code      = item.code;
                        const codeName  = item.codeName;
                        const hndlClick = () => { hndlCheck(code) };
                        const classArr   = [];
                        if(cls)                         classArr.push(cls);
                        if(cond.indexOf(code) >= 0)     classArr.push("active");
                        return <button key={`${name}${index}`} onClick={hndlClick} className={classArr.join(" ")}>{em}{codeName}</button>
                    })}
                </span>
            </div>
        )
    }
}