import { Component }    from "react";
import DaumPostcode     from "react-daum-postcode";

/**
 * 공통 - 주소 검색
 */
 export default class SearchAddress extends Component {
    /**
     * 생성자
     * @param {*} props 
     */
    constructor(props){
        super(props);

        const { fnClose, fnCallback } = this.props;
        this.state = {
            fnClose
          , fnCallback
        };
    }
        
    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidMount(){
        console.log(`############ ${this.constructor.name}.componentDidMount ############`);
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidUpdate(prevProp, prevState){
        console.log(`############ ${this.constructor.name}.componentDidUpdate ############`);
    }

    /**
     * 렌더링
     * @returns 
     */
    render(){
        const { fnClose, fnCallback } = this.state;

        // Func: 닫기 처리
        const hndlClose = () => {
            if(fnClose) fnClose();
        }
        
        // Func: 주소 검색
        const hndlCallback = data => {
            console.log(data);

            try{
                const geocoder = new window.kakao.maps.services.Geocoder();
                geocoder.addressSearch(data.address, function(result, status){
                    if(result.length > 0){
                        data.latitude   = result[0].y;
                        data.longitude  = result[0].x;
                    }

                    if(fnCallback)  fnCallback(data);
                });
            }catch(e){
                if(fnCallback)  fnCallback(data);
            } 
        }

        return (
            <div className="layer layer01" style={{display:"block"}}>
                <div className="layer-form">
                    <h5>주소 검색</h5>
                    <DaumPostcode
                        onComplete={hndlCallback}
                        autoClose={true}
                    />
                </div>
                <button onClick={hndlClose} className="btn_close">레이어 닫기</button>
            </div>
        )
    }
}