import React, { Component } from "react";
import { CustomOverlayMap, Map }              from "react-kakao-maps-sdk";

import instance             from "service/axiosInstance";
import CustomMarker         from "./CustomMarker";

import LayerMarkerForm      from "pages/front/map/layer/MarkerEdit";
import CommonUtils          from "utils/CommonUtils";

const LOCATION_DEF  = { lat : 33.450701, lng : 126.570667 }    // 기본 위치
const LEVEL_DEF     = 5;    // 기본 지도 레벨
const LEVEL_MIN     = 3;    // 최소 지도 레벨
const LEVEL_MAX     = 10;   // 최대 지도 레벨
const LIST_COLOR    = ["#6666ff", "#99cc33", "#9933ff", "#996633", "#99cc00", "#00cccc", "#3399cc", "#ff6633", "#cc3399", "#6699ff"]  // 색삭목록

/**
 * Front - 매물 지도 - Customized Map
 */
export default class CustomMap extends Component {
    /**
     * @description 생성자
     * @param {*} props 
     */
    constructor(props){
        super(props);

        const { location, markers, fnCallback } = this.props;

        this.state  = {
            location    : LOCATION_DEF  // 현재 위치
          , fnCallback                  // 이벤트 콜백
          , markers                     // 마커 목록
          , curMarker   : null          // 현재 마커
          , timer       : null
        }

        this.map    = React.createRef();    // 맵 Ref
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidMount(){
        console.log(`############ ${this.constructor.name}.componentDidMount ############`);
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidUpdate(prevProps, prevState){
        console.log(`############ ${this.constructor.name}.componentDidUpdate ############`);

        if(JSON.stringify(prevProps.markers) !== JSON.stringify(this.props.markers)){
            this.delayState({ markers:this.props.markers });
        }

        if(JSON.stringify(prevProps.curMarker) !== JSON.stringify(this.props.curMarker)){
            const curMarker = this.props.curMarker;
            if(curMarker){
                const nLocation = { lat:curMarker.latitude, lng:curMarker.longitude };
                this.setState({ curMarker, location:nLocation });
            }else{
                this.setState({ curMarker });
            }
        }
    }

    /**
     * @description 지연 설정 (빈번한 설정 막기)
     * @param {*} params 
     */
    delayState = params => {
        if(this.state.timer){
            clearTimeout(this.state.timer);
        }
        this.state.timer = setTimeout(() => { this.setState({ ...params })}, 300);
    }

    /**
     * @description 지도 Level 처리
     */
    level = {
        get     : () => this.map.current.getLevel()
      , set     : value => this.map.current.setLevel(value)
      , plus    : () => {
            const nLevel = this.level.get();
            if(nLevel > LEVEL_MIN){
                this.level.set(nLevel - 1);
            }
        }
      , minus   : () => {
            const nLevel = this.level.get();
            if(nLevel < LEVEL_MAX){
                this.level.set(nLevel + 1);
            }
        }
    };

    /**
     * @description 지도 위치 처리
     * @function get    현재 위치 정보 반환
     * @function set    현재 위치 정보 설정
     * @function bounds 현재 지도상의 동북/서남 좌표 반환
     */
    location = {
        get     : () => this.state.location
      , set     : value => this.setState({ location:value })
      , bounds  : () => {
            if(this.map.current){
                const bounds    = this.map.current.getBounds();
                const neLat     = bounds.getNorthEast().getLat();
                const neLng     = bounds.getNorthEast().getLng();
                const swLat     = bounds.getSouthWest().getLat();
                const swLng     = bounds.getSouthWest().getLng();
                const level     = this.level.get();
                
                return { neLat, neLng, swLat, swLng, level };
            }else{
                return null;
            }
      }
    }

    /**
     * @description 렌더링
     * @return
     */
    render(){
        const { location, markers, curMarker, fnCallback } = this.state;
        const level = (this.map.current)?this.map.current.getLevel():this.state.level;
        console.log(level);

        // Func: 지도 이벤트 처리
        const hndlCallback = (type, target) => {
            if(fnCallback){
                fnCallback(type, target);
            }
        }

        // Func: 마커 클릭 이벤트 처리
        const hndlClick = data => {
            hndlCallback("showmarker", data);
        }

        // Func: 지도 이동(Level / Location) 처리
        const hndlMove = (level, loc) => {
            this.map.current.setLevel(level);
            this.setState({ location:loc, markers:[] });
        }

        return (
            <Map
                ref     = { this.map }
                center  = { location }
                level   = { level }
                style   = {{ width: "100%", height: "100vh" }}
                onCenterChanged     =   {target => { hndlCallback("onCenterChanged"); }}
                onZoomChanged       =   {target => { this.setState({ markers:[] });hndlCallback("onZoomChanged"); }}
            >
                {
                    markers.map((marker, index) => {
                        if(level >= 9){
                            const loc               = { lat : marker.latitude, lng : marker.longitude };
                            const sigungu           = marker.sigungu;
                            const markerCnt         = marker.cntMarker;
                            return (
                                <CustomOverlayMap position={ loc }>
                                    <button
                                        className   = {`ic_marker03 m${CommonUtils.lpad(marker.sigunguSeq%10, 2, "0")}`}
                                        onClick     = {() => hndlMove(8, loc)}
                                    >{sigungu} ({markerCnt})</button>
                                </CustomOverlayMap>
                                                
                            )
                        } else if(level === 8) {
                            const loc               = { lat : marker.latitude, lng : marker.longitude };
                            const title             = marker.bname;
                            const markerCnt         = marker.cntMarker;
                            return (
                                <CustomOverlayMap position={ loc }>
                                    <button
                                        className   = {`ic_marker03 m${CommonUtils.lpad(marker.bnameSeq%10, 2, "0")}`}
                                        onClick     = {() => hndlMove(7, loc)}
                                    >{title} ({markerCnt})</button>
                                </CustomOverlayMap>
                            )
                        } else {
                            return (
                                <CustomMarker
                                    key         = {`marker${index}`}
                                    item        = {marker}
                                    selected    = {marker.mId === (curMarker??{}).mId?true:false}
                                    zIndex      = {marker.mId === (curMarker??{}).mId?10:0}
                                    fnCallback  = {hndlClick}
                                />
                            )
                        }
                    })
                };
            </Map>
        )
    }
}