/**
 * Front - Main - 이미지 뷰어 팝업
 */
function LayerImageViewer() {
    return (
        <div id="lyImageViewer" className="layer layer03" onclick="hideLayout('#lyImageViewer')">
            <div id="slider" className="layer-view">
                <a href="#" className="control_next" onclick="_image.slideLeft();return false;">&gt;</a>
                <a href="#" className="control_prev" onclick="_image.slideRight();return false;">&lt;</a>
                <div id="contImage"></div>
                <a href="#" className="zoon_in" onclick="_image.zoomIn();return false;">+</a>
                <a href="#" className="zoom_out" onclick="_image.zoomOut();return false;">-</a>
            </div>
            <a href="#" onclick="hideLayout('#lyImageViewer');return false;" className="btn_close">레이어 닫기</a>
        </div>
    )
}

export default LayerImageViewer;