import React, { Component } from "react"
import { Link }             from "react-router-dom";

import BoardDetail          from "./comp/BoardDetail";
import BoardList            from "./comp/BoardList";

/**
 * @path    Front - 게시판
 * @url     /board
 */
export default class Board extends Component {
    constructor(props){
        super(props);

        this.state = {
            loginUser   : {}    // 로그인 사용자
          , viewType    : ""    // 보기 모드
          , cmntSeq     : ""    // 댓글번호
          , cmntCont    : ""    // 댓글
          , bbsSeq      : null  // 선택된 게시글 번호
          , page        : 1     // 페이지 번호
          , comments    : []    // 댓글 목록
        }

        this.boardList = React.createRef();
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
    componentDidMount(){
        console.log("############ Board.componentDidMount ############");
    }
    
    render() {
        // Func: 게시글 선택
        const fnGetBbsDetail = (viewType, bbsSeq) => {
            this.setState({ viewType, bbsSeq });
        }

        return (
            <div id="board_wrap">
                <div id="bbs_header">
                    <h4>MapMarker</h4>
                    <Link to="/main"    className="btn_home">공유지도</Link>
                    <button className="btn_back" onClick={() => { fnGetBbsDetail("V", "" )}}>목록으로</button>
                </div>
                <div id="bbs_contents">
                    <h5 className="content-title"><strong>가로수이야기</strong></h5>
                    <BoardDetail viewType={this.state.viewType} bbsSeq={this.state.bbsSeq} onClose={() => { fnGetBbsDetail("V", null); }}/>
                    <BoardList ref={this.boardList} bbsSeq={this.state.bbsSeq} onClick={fnGetBbsDetail} />
                </div>
            </div>
        )
    }
}