import { Component } from "react";

const DEFAULT_LIST_SIZE = 10;   // 기본 목록 개수
const DEFAULT_PAGE_SIZE = 5;    // 기본 페이지 개수
const DEFAULT_FIRST_PAGE = 1;   // 기본 첫 페이지

/**
 * 공통 페이징 처리
 */
export default class Pager extends Component {
    constructor(props){
        super(props);

        const { listSize, pageSize, page, totalCount, onClick } = props;

        this.state = {
            listSize    : listSize??DEFAULT_LIST_SIZE       // 한 페이지당 보여줄 목록 개수
          , pageSize    : pageSize??DEFAULT_PAGE_SIZE       // 한 화면에 보여줄 페이지 개수
          , page        : page??DEFAULT_FIRST_PAGE          // 현재 페이지
          , totalCount  : totalCount??0                     // 전체 목록 개수
          , onClick     : onClick                           // 페이지 클릭 이벤트
        }
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidMount(){
        // console.log("############ Pager.componentDidMount ############");
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
    componentDidUpdate(prevProp, prevState){
        // console.log("############ Pager.componentDidUpdate ############");
        if(JSON.stringify(prevProp) !== JSON.stringify(this.props)){
            const { listSize, pageSize, page, totalCount, onClick } = this.props;

            this.setState({
                listSize    : listSize??DEFAULT_LIST_SIZE       // 한 페이지당 보여줄 목록 개수
              , pageSize    : pageSize??DEFAULT_PAGE_SIZE       // 한 화면에 보여줄 페이지 개수
              , page        : page??DEFAULT_FIRST_PAGE          // 현재 페이지
              , totalCount  : totalCount??0                     // 전체 목록 개수
              , onClick     : onClick                           // 페이지 클릭 이벤트
            });
        }
    }

    /**
     * @description 렌더링
     */
    render(){
        const { listSize, pageSize, page, totalCount, onClick } = this.state;

        const pageNumbers   = [];

        const totalPage     = Math.ceil(totalCount / listSize);
        const startPage     = parseInt(((page - 1) / pageSize)) * pageSize + 1;   // 시작 페이지
        let endPage         = (parseInt((page - 1) / pageSize) + 1) * pageSize; // 종료 페이지

        // 마지막 페이지가 전체 페이지 번호 보다 큰 경우 마지막 페이지를 전체 페이지 번호로 변경
        if(endPage > totalPage) endPage = totalPage;

        // 페이지 번호 목록 정리
        for(let nPage = startPage; nPage <= endPage; nPage++){
            pageNumbers.push(nPage);
        }

        // 페이징 처리
        const hndlPage = (nPage) => {
            if(onClick !== undefined)   onClick(nPage);
        }

        // Element 출력
        return (
            <div className="pagination">
                {page !== 1?<button title="이전 페이지" onClick={() => hndlPage(page - 1)}>{`<`}</button>:null}
                {pageNumbers.map(nPage => <button key={`page${nPage}`} title={`${nPage} 페이지`} onClick={() => hndlPage(nPage)} className={nPage === page?"selected":""}>{nPage}</button>)}
                {page < totalPage?<button title="다음 페이지" onClick={() => hndlPage(page + 1)}>{`>`}</button>:null}
            </div>
       )
    }
}