import { Component }    from "react"
import MultiRangeSlider from "multi-range-slider-react";   // Multi Range Slider

/**
 * @description 가격 조건
 */
export default class CondPrice extends Component {
    /**
     * @description 생성자
     * @param {*} props 
     */
    constructor(props){
        super(props);

        const { title, min, max, minValue, maxValue, fnCallback } = props;
        this.state = { 
            title, min, max, minValue, maxValue, fnCallback
          , timer : null
        };
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidMount(){
        // console.log(`############ ${this.constructor.name}.componentDidMount ############`);
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
    componentDidUpdate(prevProps, prevState){
        // console.log(`############ ${this.constructor.name}.componentDidUpdate ############`);
        if(JSON.stringify(prevProps) !== JSON.stringify(this.props)){
            const { min, max, minValue, maxValue, fnCallback } = this.props;
            this.setState({ min, max, minValue, maxValue, fnCallback });
        }
    }
    
    /**
     * @description 렌더링
     * @returns 
     */
    render() {
        const { title, min, max, minValue, maxValue, fnCallback } = this.state;

        // Func: Range 변경 이벤트 처리
        const hndlChange = (stValue, edValue) => {
            if(this.state.minValue !== stValue || this.state.maxValue !== edValue){
                if(fnCallback)  fnCallback(stValue, edValue);
            }
            this.setState({ timer:null });
        }

        // Func: 입력 처리
        const hndlInput = e => {
            if(this.state.timer !== null)   clearTimeout(this.state.timer);

            const stValue = e.minValue;
            const edValue = e.maxValue;
            if(this.state.minValue !== stValue || this.state.maxValue !== edValue){
                hndlChange(stValue, edValue );
            }
        }
        
        // Step. Element 반환
        return (
            <div className="div_price">
                <label>
                    <strong>{title}</strong>
                    <em><input type="number" value={minValue} onFocus={e => e.target.select()} onChange={e => { hndlChange(e.target.value, this.state.maxValue)}}/>만원</em>
                    <i>~</i>
                    <em><input type="number" value={maxValue} onFocus={e => e.target.select()} onChange={e => { hndlChange(this.state.minValue, e.target.value)}}/>만원</em>
                </label>
                <MultiRangeSlider
                    min             = {min}
                    max             = {max}
                    minValue        = {minValue}
                    maxValue        = {maxValue}
                    ruler           = {false}
                    label           = {false}
                    thumbLeftColor  = {"#3333cc"}
                    thumbRightColor = {"#3333cc"}
                    barInnerColor   = {"#3333cc"}
                    onInput        = {e => hndlInput(e)}
                />
            </div>
        )
    }
}