import { Component }        from "react"

/**
 * 개인지도
 */
export default class Bookmark extends Component {
    constructor(props){
        super(props);

        this.state = {
            layer:{
                search      : false     // 마커 검색 레이어
              , location    : false     // 위치 레이어
              , bookmark    : false     // 북마크 추가 레이어
              , address     : false     // 주소 검색 레이어
              , share       : false     // 공유하기 레이어
              , editmarker  : false     // 마커 관리 레이어
            }
          , addmarker       : false     // 지도 중앙 마커 추가
          , cond: {
                openYn      : false     // 오픈 예정 만 보기
              , selfYn      : false     // 내가 등록 한 마커 만 보기
            }
          , location: {
                lat         : 33.450701
              , lng         : 126.570667
            }
          , level           : 3         // 지도 레벨
        }
    }

    render() {
        // Func: 대상 레이어 팝업 오픈
        const fnOpenLayer = (e) => {
            e.preventDefault();
            let target = e.target.target;
            console.log(target);
            this.setState(prevState => ({ layer:{ ...prevState.layer, [target]:true }}))
        }

        // Func: 대상 레이어 팝업 닫기
        const fnCloseLayer = target => {
            this.setState(prevState => ({ layer:{ ...prevState.layer, [target]:false }}))
            console.log(this.state.cond)
        }

        // Func: 검색 조건 변경
        const fnChangeState = (key, value) => {
            this.setState(prevState => ({ cond:{ ...prevState.cond, [key]:value }}))
        }

        // Func: 현재 위치 이동 변경
        const fnTraceSuccess = (position) => {
            const lat = position.coords.latitude;
            const lng = position.coords.longitude;

            console.log('위도 : ' + lat + ' 경도 : ' + lng);
            this.setState({location:{lat, lng}});
        }
        const fnTraceError = error => {
            console.error(error);
        }
        const fnDoTrace = (e) => {
            e.preventDefault();

            if (navigator.geolocation) { // GPS를 지원하면
                navigator.geolocation.getCurrentPosition(fnTraceSuccess, fnTraceError, {
                    enableHighAccuracy  : false
                  , maximumAge          : 0
                  , timeout             : Infinity
                });
            } else {
                alert('GPS를 지원하지 않습니다');
                return;
            }
            
        }

        return (
            <>
            </>
        )
    }
}