import { createSlice } from '@reduxjs/toolkit';

export const tokenSlice = createSlice({
    name: 'base'
  , initialState: {
        codeList        : []      // 기본 코드 목록
    }
  , reducers: {
        SET_CODE: (state, action) => {
            state.codeList    = action.payload;
        }
  }
})

export const { SET_CODE } = tokenSlice.actions;

export default tokenSlice.reducer;