import { Component }        from "react";
import { CustomOverlayMap } from "react-kakao-maps-sdk";

import CommonUtils          from "utils/CommonUtils"

const houseTypeClass    = { 'C0401': 'h01', 'C0402': 'h02', 'C0403': 'h03', 'C0404': 'h04', 'C0405': 'h05', 'C0406': 'h06', 'C0407': 'h07', 'C0408': 'h08', 'C0409': 'h09', 'C0410': 'h10' }
const elementCntrt      = {
    'C0101': { text: "매매", clsName: "m01" }
  , 'C0102': { text: "전세", clsName: "m02" }
  , 'C0103': { text: "월세", clsName: "m03" }
  , 'C0104': { text: "기타", clsName: "m04" }
}

/**
 * 맵 마커 커스터마이즈
 */
export default class CustomMarker extends Component {
    /**
     * 생성자
     * @param {*} props 
     */
    constructor(props){
        super(props);

        const { item, selected, zIndex, fnCallback } = props;
        this.state = { item, selected, zIndex, fnCallback };
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidMount(){
        // console.log(`############ ${this.constructor.name}.componentDidMount ############`);
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
    componentDidUpdate(prevProps, prevState){
        // console.log(`############ ${this.constructor.name}.componentDidUpdate ############`);

        if(
             JSON.stringify(prevProps.item) !== JSON.stringify(this.props.item)
          || prevProps.selected             !== this.props.selected
          || prevProps.zIndex               !== this.props.zIndex
        ){
            const { item, selected, zIndex, fnCallback } = this.props;
            this.setState({ item, selected, zIndex, fnCallback });
        }
    }

    /**
     * 렌더링
     * @returns 
     */
    render(){
        const { item, selected, zIndex, fnCallback } = this.props;

        // Func: 클릭 처리
        const hndlClick = data => { if(fnCallback)  fnCallback(data); }

        // 마커의 위도/경도 위치
        const location  = { lat : item.latitude, lng : item.longitude };

        // 주거 유형
        const houseType = houseTypeClass[item.houseType];

        // Step. 마커 클래스 정보 세팅
        const markerClass = [];
        markerClass.push("ic_marker02");
        if(selected === true)   markerClass.push("active");
        // markerClass.push("active");

        // Step. [Active] 리베이터 / 거주상태 세팅
        const additional = [];
        if (item.rebateTrade > 0 && item.rebateTrade > '')  additional.push(item.rebateTrade);
        if (item.residenceTypeName > '')                    additional.push(item.residenceTypeName);
        if (item.rebateRent > 0 && item.rebateRent > '')    additional.push(item.rebateRent);

        // Step. [Active] 상세주소 처리
        const addrDtl   = item.addrDtl??"";
        const addrDtl1  = addrDtl.substring(0, 4);
        const addrDtl2  = addrDtl.substring(0, 20);
        
        // Step. 계약 상태 처리
        const contract = [];    // 계약 상태 [Unactive]
        if (CommonUtils.isEmpty(item.cntrtCode) !== true) {
            const cntrtList = item.cntrtCode.split(",");
            for (let idx = 0; idx < cntrtList.length; idx++) {
                const cntrtCode = cntrtList[idx];
                if (elementCntrt[cntrtCode]){
                    const { text, clsName } = elementCntrt[cntrtCode];
                    if(selected === true)   contract.push(<u key={`${item.mId}_${idx}`} className={`${clsName} dtl`}>{text}</u>)
                    else                    contract.push(<u key={`${item.mId}_${idx}`} className={`${clsName}`}>{text.substring(0, 1)}</u>)
                }    
            }
        }

        // Step. 월세가 있는 경우 월세를 보여줌, 없는 경우 전세
        let priceInfo = "";
        if (item.priceMonthly !== '' && item.priceMonthly > 0)
            priceInfo = CommonUtils.fmtCurrency(item.priceMonthly, 4);
        else
            priceInfo = CommonUtils.fmtCurrency(item.price, 4);

        // Step. 방 + 화장실 + 시설 정보
        const titleS = [];
        const titleL = [];
        if (item.cntRoom !== '0') {
            titleS.push(`방${item.cntRoom}`);
            titleL.push(`방${item.cntRoom}`);
        }
        if (item.cntToilet !== '0') {
            titleS.push(`화${item.cntToilet}`);
            titleL.push(`화${item.cntToilet}`);
        }
        if (CommonUtils.isEmpty(item.facilities) !== true) {
            const facilityList = item.facilities.split(",");
            for (let idx = 0; idx < facilityList.length; idx++) {
                const facility = facilityList[idx];
                if (facility === 'C0601') {
                    titleS.push('테');
                    titleL.push('테라스');
                } else if (facility === 'C0602') {
                    titleS.push('엘');
                    titleL.push('엘베');
                }
            }
        }

        // Step. 전용면적 + 연식
        const etc = [];
        if (item.areaPrivate !== null && item.areaPrivate !== '') {
            etc.push(`전용 ${item.areaPrivate}평`);
        }
        if (item.areaSupply !== null && item.areaSupply !== '') {
            etc.push(`연식 ${item.areaSupply}년`);
        }

        return (
            <CustomOverlayMap position={ location } zIndex={ zIndex }>
                <button className={markerClass.join(" ")} onClick={() => hndlClick(item)}>
                    <em></em>
                    <span className="marker-content">
                        <span className="marker-additional">{additional.join("\n")}</span>
                        <span className={"marker-address " + houseType??""}>{addrDtl1}</span>
                        <span className={"marker-address-dtl " + houseType??""}>{addrDtl2}</span>
                        {contract}
                        <i className={"price " + houseType??""}>{priceInfo}</i>
                        <span className="tit-marker">{titleS.join(" ")}</span>
                        <span className="tit-marker-dtl">
                            {titleL.join(" ")}
                            {titleL.length > 0 && etc.length > 0?<br/>:null}
                            {etc.join(" / ")}
                        </span>
                    </span>
                </button>
            </CustomOverlayMap>
        )        
    }
}