import { Component }    from "react";

export default class Menu extends Component {
    /**
     * 생성자
     * @param {*} props 
     */
    constructor(props){
        super(props);

        const { target, openYn, selfYn, onClick } = props;
        this.state = {
            target
          , openYn
          , selfYn
          , onClick
          , addmarker   : false // 추가용 마커 보일지 여부
        };
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidMount(){
        // console.log(`############ ${this.constructor.name}.componentDidMount ############`);
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
    componentDidUpdate(prevProps, prevState){
        // console.log(`############ ${this.constructor.name}.componentDidUpdate ############`);
        if(JSON.stringify(prevProps) !== JSON.stringify(this.props)){
            const { target, openYn, selfYn, onClick } = this.props;
            this.setState({ target, openYn, selfYn, onClick, editmarker:false });
        }
    }


    /**
     * 렌더링
     */
    render(){
        const { target, openYn, selfYn, onClick, addmarker } = this.state;

        // 메뉴목록
        const menus = {
            menu        : { clsName:["btn_map", "menu"]             , text:"메뉴", image:"/images/app/ic_menu_bk.png" }
          , search      : { clsName:["btn_map", "m_search"]         , text:"마커\n검색" }
          , address     : { clsName:["btn_map", "a_search"]         , text:"마커\n추가" }
          , main        : { clsName:["btn_map", "home"]             , text:"매물\n지도" }
          , bookmark    : { clsName:["btn_map", "bookmark"]         , text:"개인\n지도" }
      
          , openYn      : { clsName:["btn_map", "open"]             , text:"오픈\n예정" }
          , selfYn      : { clsName:["btn_map", "my_marker"]        , text:"MY\n마커" }
      
          , location    : { clsName:["btn_map", "location"]         , text:"지도\n이동" }
          , board       : { clsName:["btn_map", "board"]            , text:"게시판" }
          , friend      : { clsName:["btn_map", "friend"]           , text:"친구\n목록" }
          , mypage      : { clsName:["btn_map", "login"]            , text:"마이\n페이지" }
          , trace       : { clsName:["btn_map", "trace"]            , text:"현재\n위치" }
          , editmarker  : { clsName:["btn_map", "center_marker"]    , text:"추가용 가운데 핀" }
          , addmarker   : { clsName:["btn_map", "add_marker"]       , text:"+" }
      
          , levelPlus   : { clsName:["btn_ctrl", "view_plus"]       , text:"+" }
          , levelMinus  : { clsName:["btn_ctrl", "view_minus"]      , text:"-" }
      };

        if(target === "main")   menus.main.clsName.push("active");
        if(openYn === true)     menus.openYn.clsName.push("active");
        if(selfYn === true)     menus.selfYn.clsName.push("active");
        if(addmarker == true)   menus.editmarker.clsName.push("active");
        
        // <Link to="#openYn"      onClick={e => {e.preventDefault();fnChangeState("openYn", !this.state.cond.openYn); }} className={this.state.cond.openYn === true?"btn_map open active":"btn_map open"}>오픈<br/>예정</Link>
        // <Link to="#selfYn"      onClick={e => {e.preventDefault();fnChangeState("selfYn", !this.state.cond.selfYn); }} className={this.state.cond.selfYn === true?"btn_map my_marker active":"btn_map my_marker"}>MY<br/>마커</Link>
        // {this.state.addmarker === true?<Link to="#editmarker" target="editmarker" onClick={fnOpenLayer} className="btn_map center_marker active">추가용 가운데 핀</Link>:null }

        // Func: 클릭 시 이벤트 처리
        const hndlClick = key => { 
            if(key === "addmarker"){
                this.setState({ addmarker:!addmarker });
            } else if(key === "editmarker") {
                this.setState({ addmarker:false });
                if(onClick)    onClick(key);
            } else {
                if(onClick)    onClick(key);
            }
        }

        return (
            <>
                {Object.keys(menus).map(key => {
                    const menu = menus[key];
                    return (
                        <button
                            key         = {key}
                            onClick     = {() => hndlClick(key)}
                            className   = {menu.clsName.join(" ")}
                        >{menu.image?<img alt={menu.text} src={menu.image}/>:menu.text}</button>
                    )
                })}
            </>
        )
    }
}