import { useEffect, useState }                  from 'react';
import { useDispatch, useSelector }             from 'react-redux';

import * as cookie                              from 'storage/Cookie';
import { requestToken }                         from 'service/UserService';
import * as auth                                from 'store/Auth';
import common                                   from 'utils/CommonUtils';

export function CheckToken(key) {
    const [ isAuth, setIsAuth ]         = useState('Loading');
    const authenticated                 = useSelector(state => state.authToken.authenticated);
    const expireTime                    = useSelector(state => state.authToken.expireTime);
    const dispatch                      = useDispatch();

    useEffect(()=> {
        const checkAuthToken = async () => {
            const userId        = cookie.getUserId();
            const refreshToken  = cookie.getRefreshToken();

            // 개발모드인 경우 Console 출력
            if(common.devYn()){
                console.log("$$$$$$$$$$$$$$$$$$")
                console.log(userId);
                console.log(authenticated);
                console.log(expireTime);
                console.log(refreshToken);
                console.log("$$$$$$$$$$$$$$$$$$")
            }

            if (refreshToken === undefined) {
                dispatch(auth.DELETE_TOKEN());
                dispatch(auth.DELETE_USER());
                setIsAuth('Failed');
            } else {
                if (authenticated === true && new Date().getTime() < expireTime){
                    setIsAuth('Success');
                } else {
                    const response = await requestToken(userId, refreshToken);
                    if (response.status === true) {
                        const { accessToken, loginUser } = response.json;
                        dispatch(auth.SET_TOKEN(accessToken));
                        dispatch(auth.SET_USER(loginUser));
                        setIsAuth('Success');
                    } else {
                        dispatch(auth.DELETE_TOKEN());
                        dispatch(auth.DELETE_USER());
                        cookie.removeRefreshToken();
                        cookie.removeUserId();
                        setIsAuth('Failed');
                    }
                }
            }
        };
        checkAuthToken();
    }, [authenticated, expireTime, dispatch, key]);

    return {
        isAuth
    };
}