import React, { Component }    from "react";

import instance         from "service/axiosInstance";
import CodeService      from "service/CodeService";
import CommonUtils      from "utils/CommonUtils";
import CondCheck        from "./comp/CondCheck";
import CondPrice        from "./comp/CondPrice";
import SearchItem from "./comp/SearchItem";

/**
 * Front - Main - 마커 검색 팝업
 */
export default class MarkerSearch extends Component {
    /**
     * @description 생성자
     * @param {*} props 
     */
    constructor(props) {
        super(props);

        const { callback }  = props;

        this.state = {
            list                : []            // 검색 결과
          , callback
            // 검색 조건
          , cond                : { 
                searchTerm      : ""
              , houseType       : []
              , cntrtCode       : []
              , newYn           : []
              , cntRoom         : []
              , cntToilet       : []
              , facilities      : []
              , loanType        : []
              , orderBy         : "P"
            }
            // 콤보 목록
          , combo               : { 
                searchTerm      : ""
              , houseType       : []
              , cntrtCode       : []
              , newYn           : [{code:"N", codeName:"구축"}, {code:"Y", codeName:"신축"}]    // 건물상태
              , cntRoom         : [{code:"0", codeName:"0"}, {code:"1", codeName:"1"}, {code:"2", codeName:"2"}, {code:"3", codeName:"3"}, {code:"4", codeName:"4"}, {code:"5~", codeName:"5 ~"}]
              , cntToilet       : [{code:"0", codeName:"0"}, {code:"1", codeName:"1"}, {code:"2", codeName:"2"}, {code:"3", codeName:"3"}, {code:"4", codeName:"4"}, {code:"5~", codeName:"5 ~"}]
              , facilities      : []
              , loanType        : [{code:"X", codeName:"해당없음"}, {code:"A", codeName:"안심"}, { code:"C", codeName:"중기청"}, { code:"L", codeName:"LH"}, { code:"S", codeName:"SH"}]    // 대출 유형
              , priceSt         : 0
              , priceEd         : 100
              , priceMonthlySt  : 0
              , priceMonthlyEd  : 100
            }
          , timer               : null
        }

        this.keyBox  = React.createRef();
        this.listBox = React.createRef();
    }


    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidMount(){
        console.log(`############ ${this.constructor.name}.componentDidMount ############`);

        // 부동산유형[P0004] / 거주유형[P0005] / 계약유형[P0001] / 중요옵션[P0006]
        CodeService.getCodeList("P0004|P0005|P0001|P0006").then(list => {
            const houseType     = [];
            const residenceType = [];
            const cntrtCode     = [];
            const facilities    = [];

            houseType.push({code:"X", codeName:"해당없음"});

            for(let code of list){
                if(code.parntCode === "P0004")  houseType.push(code);
                if(code.parntCode === "P0005")  residenceType.push(code);
                if(code.parntCode === "P0001")  cntrtCode.push(code);
                if(code.parntCode === "P0006")  facilities.push(code);
            }

            facilities.push({code:"X", codeName:"해당없음"});

            this.setState(prevState => ({ combo:{ ...prevState.combo, houseType, residenceType, cntrtCode, facilities } }))

            // 내 조건 조회
            instance.post("/api/main/marker/cond")
            .then(res   => {
                const list              = res.data;
                const priceInfo         = list[0][0];
                let myCond              = this.fnAllCheck();
                myCond.priceSt          = priceInfo.minPrice;
                myCond.priceEd          = priceInfo.maxPrice;
                myCond.priceMonthlySt   = priceInfo.minPriceMonthly;
                myCond.priceMonthlyEd   = priceInfo.maxPriceMonthly;
                myCond.orderBy          = "D";
                if(list[1][0].condition01){
                    myCond = JSON.parse(list[1][0].condition01);
                }
                
                this.setState(prevState => ({ combo:{ ...prevState.combo, priceSt:priceInfo.minPrice, priceEd:priceInfo.maxPrice, priceMonthlySt:priceInfo.minPriceMonthly, priceMonthlyEd:priceInfo.maxPriceMonthly }, cond:myCond }))

                if(this.keyBox.current){
                    this.keyBox.current.focus();
                }
            });
        });
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
    componentDidUpdate(prevProps, prevState){
        console.log(`############ ${this.constructor.name}.componentDidUpdate ############`);

        if(JSON.stringify(prevState.cond) !== JSON.stringify(this.state.cond)){
            this.fnDelaySearch();
        }
    }

    /**
     * @description 지연 검색 (트래픽 부하 방지)
     */
     fnDelaySearch = () => {
        if(this.state.timer !== null){
            clearTimeout(this.state.timer);
        }

        this.setState({ timer:setTimeout(() => { this.fnSearch(); }, 500) });
    }
    
    /**
     * @description 마커 상세 정보 검색
     */
    fnSearch = () => {
        const { searchTerm } = this.state.cond;
        if(CommonUtils.isEmpty(searchTerm) === false){
            instance
            .post("/api/main/marker/search", this.state.cond, {headers: { 'Content-Type': 'application/json;charset=UTF-8' }})
            .then(res       => res.data)
            .then(list      => this.setState({ list }))
            .catch(e        => {
                console.log(e);
            });
        }else{
            this.setState({ list:[] });
        }
    }

    // Func: 전체 선택 해제
    fnAllUnCheck = () => {
        return { 
              houseType       : []
            , cntrtCode       : []
            , newYn           : []
            , cntRoom         : []
            , cntToilet       : []
            , facilities      : []
            , loanType        : []
        };
    };

    // Func: 조건 전체 선택
    fnAllCheck = () => {
        const cond  = this.fnAllUnCheck();

        const combo = this.state.combo;
        for(let code of combo.houseType)    cond.houseType.push(code.code);
        for(let code of combo.cntrtCode)    cond.cntrtCode.push(code.code);
        for(let code of combo.newYn)        cond.newYn.push(code.code);
        for(let code of combo.cntRoom)      cond.cntRoom.push(code.code);
        for(let code of combo.cntToilet)    cond.cntToilet.push(code.code);
        for(let code of combo.facilities)   cond.facilities.push(code.code);
        for(let code of combo.loanType)     cond.loanType.push(code.code);

        return cond;
    }

    // Func: 전체 선택 여부
    fnAllCheckYn = () => {
        const cond  = this.state.cond;
        const combo = this.state.combo;
        if(cond.houseType.length    !== combo.houseType.length)     return false;
        if(cond.cntrtCode.length    !== combo.cntrtCode.length)     return false;
        if(cond.newYn.length        !== combo.newYn.length)         return false;
        if(cond.cntRoom.length      !== combo.cntRoom.length)       return false;
        if(cond.cntToilet.length    !== combo.cntToilet.length)     return false;
        if(cond.facilities.length   !== combo.facilities.length)    return false;
        if(cond.loanType.length     !== combo.loanType.length)      return false;

        return true;
    }

    /**
     * @description 렌더링
     * @returns 
     */
    render(){
        const { list, cond, combo, callback } = this.state;
        
        // Func: 레이어 닫기
        const hndlClose = () => { if(callback)  callback("close"); }
        
        // Func: 파라미터 세팅
        const hndlCond = (key, value) => { this.setState(prevState => ({cond:{ ...prevState.cond, [key]:value }})) };

        // Func: 파라미터 Toggle [Checkbox]
        const hndlCheck = (key, value) => {
            const param = [];

            let already = false;
            if(this.state.cond[key]){
                for(let code of this.state.cond[key]){
                    if(code === value){
                        already = true;
                    }else{
                        param.push(code);
                    }
                }
            }
            if(already === false)   param.push(value);
            param.sort();
            this.setState(prevState => ({ cond:{ ...prevState.cond, [key]:param }}));
        }

        // Func: 조건 전체 선택/해제 토글
        const toggleAllCheck = () => {
            if(this.fnAllCheckYn() === true){
                const param = this.fnAllUnCheck();
                this.setState(prevState => ({ cond:{ ...prevState.cond, ...param} }));
            }else{
                const param = this.fnAllCheck();
                this.setState(prevState => ({ cond:{ ...prevState.cond, ...param} }));
            }
        }

        // Func: 초기화 처리
        const hndlReset = () => {
            const param = this.fnAllCheck();
            param.searchTerm        = "";
            param.priceSt           = combo.priceSt;
            param.priceEd           = combo.priceEd;
            param.priceMonthlySt    = combo.priceMonthlySt;
            param.priceMonthlyEd    = combo.priceMonthlyEd;
            param.orderBy           = "P";
            this.setState({ cond:param });
        }

        // Func: 아이템 클릭 콜백
        const hndlItem = (type, item) => {
            if(callback)    callback(type, item);
        }

        // 콤보 목록
        const comboList = [
            { type : "cbo-house_type"   , name : "houseType"    , dot       : "dot dot#seq"     , title : "부동산 유형"}
          , { type : "cbo-house_type02" , name : "cntrtCode"    , clsName   : "ic_type02_#seq"  , title : "계약 유형" }
          , { type : "cbo-house_type02" , name : "newYn"        , clsName   : "ic_type01_#seq"  , title : "건물상태" }
          , { type : "cbo-house_type02" , name : "cntRoom"      , title     : "방 개수" }
          , { type : "cbo-house_type02" , name : "cntToilet"    , title     : "화장실 개수" }
          , { type : "cbo-house_type02" , name : "facilities"   , title     : "옵션" }
          , { type : "cbo-house_type02" , name : "loanType"     , title     : "대출유형" }
        ];

        // 가격 범위 목록
        const rangeList = [
            { minName : "priceSt"         , maxName : "priceEd"         , title : "가격" }
          , { minName : "priceMonthlySt"  , maxName : "priceMonthlyEd"  , title : "월세" }
        ];

        // 전체 체크 여부
        const wholeChecked = this.fnAllCheckYn();
        
        return (
            <div className="layer layer01" style={{display:"block"}}>
                <div className="layer-search02">
                    <button className="btn_reset" onClick={() => hndlReset()}>초기화</button>
                    <div className="search-input">
                        <label htmlFor="markerSearchKeywords">키워드</label>
                        <input type="text" id="markerSearchKeywords" name="searchTerm" value={cond.searchTerm} placeholder="예) 서울,주차,엘리베이터" onChange={e => { hndlCond(e.target.name, e.target.value) }} ref={this.keyBox}/>
                    </div>
                    <button className={wholeChecked === true?"chk_all active":"chk_all"} onClick={() => toggleAllCheck()}><em></em><span>모두 선택 및 해제</span></button>
                    <div className="search-detail">
                        {comboList.map((item, index) =>
                            <CondCheck
                                key         = {`condCheck${index}`}
                                title       = {item.title}
                                type        = {item.type}
                                name        = {item.name}
                                dot         = {item.dot}
                                clsName     = {item.clsName}
                                list        = {combo[item.name]??[]}
                                cond        = {cond[item.name]??[]}
                                fnCallback  = {hndlCheck}
                            />
                        )}
                        {rangeList.map((item, index) =>
                            (cond[item.minName] !== undefined && cond[item.maxName] !== undefined)?
                            <CondPrice
                                key         = {`condPrice${index}`}
                                title       = {item.title}
                                min         = {combo[item.minName]}
                                max         = {combo[item.maxName]}
                                minValue    = {cond[item.minName]}
                                maxValue    = {cond[item.maxName]}
                                fnCallback  = {(minValue, maxValue) => {
                                    if(cond[item.minName] !== minValue || cond[item.maxName] !== maxValue){
                                        this.setState(prevState => ({ cond:{ ...prevState.cond, [item.minName]:minValue, [item.maxName]:maxValue}}))
                                    }
                                }}
                            />
                            :null
                        )}
                    </div>
                    <div className="search-option">
                        <div className="option-switch">
                            <button className={cond.orderBy === "D"?'active':null} onClick={e => hndlCond("orderBy", "D")}>최신순</button>
                            <button className={cond.orderBy !== "D"?'active':null} onClick={e => hndlCond("orderBy", "P")}>가격순</button>
                        </div>
                    </div>
                    <SearchItem list={list} keyword={cond.searchTerm} fnCallback={hndlItem}/>
                </div>
                <button onClick={hndlClose} className="btn_close">레이어 닫기</button>
            </div>
        )
    }
}