import { useEffect, useState }  from "react"
import { Link, useNavigate }    from "react-router-dom"
import { useDispatch }          from 'react-redux';
import { useForm }              from "react-hook-form";

import Footer                   from "../menu/Footer";

import { login }                from 'service/UserService';
import { setRefreshToken, setUserId }  from 'storage/Cookie';
import * as auth                from 'store/Auth';

/**
 * Front - 로그인
 */
function Login() {
    const { errMsg, setErrMsg } = useState(null);   // 에러메시지 지정
    const navigate              = useNavigate();
    const dispatch              = useDispatch();

    // useForm 사용을 위한 선언
    const { register, setValue, formState: { errors }, handleSubmit, setFocus } = useForm();

    const init = {
        userId        : ""    // 사용자 아이디
        , emailServer : ""    // 사용자 아이디 이메일 서버 주소
        , rememberYn  : false // 자동로그인 여부
        , retUrl      : ""    // 로그인 리다이렉트 URL
    }

    // submit 이후 동작할 코드
    // 백으로 유저 정보 전달
    const onValid = async (data) => {
        // Step. 로그인 계정 세팅
        if(data.emailServer === "") data.userId = data.id;
        else                        data.userId = data.id + "@" + data.emailServer;

        const response = await login(data);
        console.log(response);
        if (response.status) {
            // 쿠키에 Refresh Token, store에 Access Token 저장
            const { refresh_token, access_token, userId, loginUser } = response.json;
            setRefreshToken(refresh_token);
            setUserId(userId);
            dispatch(auth.SET_TOKEN(access_token));
            dispatch(auth.SET_USER(loginUser));

            return navigate("/");
        } else {
            setErrMsg(response.json.error.message);
        }
        
        // input 태그 값 비워주는 코드
        setValue("userPwd", "");
    };
    
    // Email Server 목록
    const servers = ["naver.com", "daum.net", "gmail.com", "nate.com", "hanmail.com"];

    // Key Event Handler
    const hndlKeyUp = e => {
        if(e.key === 'Enter'){
            if(e.target.name === "id"){
                // 이메일주소
            }else if(e.target.name === "userPwd"){
                // 비밀번호
                // handleSubmit(onValid)
            }
            // console.log(e.target);
        }
    }

    // 최초 focus 처리
    useEffect(() => {
        setFocus("id");
    }, [setFocus]);

    return (
        <>
            <div id="login-wrap">
                <div className="form-login">
                    <h4>&nbsp;</h4>
                    <form onSubmit={handleSubmit(onValid)}>
                        <input type="hidden" {...register("userId")}/>
                        <input type="hidden" {...register("rememberYn")}/>
                        <input type="hidden" {...register("retUrl")}/>
                        <ul>
                            <li>
                                <input {...register("id", {required:"Please Enter Your ID"})} type="text" placeholder="이메일주소" className="email-addr" onKeyUp={hndlKeyUp}/>
                                <select {...register("emailServer")} className="email-server">
                                    <option value="">직접입력</option>
                                    {
                                        servers.map(server => (
                                            <option key={"opt" + server} value={server}>{server}</option>
                                        ))
                                    }
                                </select>
                            </li>
                            <li><input {...register("userPwd", {required:"Please Enter Your Password"})} type="password" placeholder="비밀번호" onKeyUp={hndlKeyUp}/></li>
                            <li><Link onClick="toggleRemember(this);return false;" className={'btn_idsave active'}><em>자동로그인</em>자동로그인</Link></li>
                        </ul>
                    </form>
                    <Link className="btn_login" onClick={handleSubmit(onValid)}><img src="/images/contents/btn_login.png"/></Link>
                    <Link to="/join" className="btn_join">회원가입</Link>
                    <p className="btn_find">
                        <Link to="/find/id">아이디 찾기</Link>
                        <Link to="/find/pwd">비밀번호 찾기</Link>
                    </p>
                </div>
            </div>
            <Footer />
        </>
    )
}

export default Login;