import { Component }        from "react"
import { Link }             from "react-router-dom";

import instance             from "service/axiosInstance";
import CommonUtils          from "utils/CommonUtils";

const IMAGE_SERVER = process.env.REACT_APP_IMAGE_SERVER;

/**
 * @path    Front - 친구목록
 * @url     /friend
 */
export default class Friend extends Component {
    constructor(props){
        super(props);

        this.state = {
            friendId    : ""    // 친구아이디
          , list        : []    // 목록
          , timer       : null
        }
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidMount(){
        console.log(`############ ${this.constructor.name}.componentDidMount ############`);
        this.fnDelaySearch();
    }

    /**
     * @description 컴포넌트가 마운트 된 경우
     */
     componentDidUpdate(prevProp, prevState){
        console.log(`############ ${this.constructor.name}.componentDidUpdate ############`);
    }

    /**
     * @description 지연 검색 (트래픽 부하 방지)
     */
    fnDelaySearch = () => {
        if(this.state.timer !== null){
            clearTimeout(this.state.timer);
        }

        const timer = setTimeout(() => { this.fnSearch(); }, 50);
        this.setState({ timer });
    }

    /**
     * 검색
     */
    fnSearch = () => {
        instance.get("/api/friend/list")
        .then(res => {
            this.setState({list:res.data});
        })
        .catch(error => {
            if(error.response === 'jwt must be provided'){
                this.fnSearch();
            }

            if(error.response){
                console.error(error.response);
            }else if(error.request){
                console.log(error.request);
            }else{
                console.log('Error', error.message);
            }
            console.log(error.config);
            // window.location.replace("/");
            // this.props.navigation.navigate("/");
        })
    }

    /**
     * 렌더링
     * @returns 
     */
    render() {
        // Func: 친구 목록 보기
        const fnShowList = e => {
            if(e.key === "Enter")   fnAddFriend();
            else                    this.setState({friendId:e.target.value});
        }
        
        // Func: 친구 추가
        const fnAddFriend = () => {
            if(CommonUtils.isEmpty(this.state.friendId)){
                alert("추가할 친구의 이메일 주소를 입력해주세요.");
            }else{
                const friendId = this.state.friendId.trim();
                instance.post("/api/friend/add", { friendId }, {headers: { 'Content-Type': 'application/json;charset=UTF-8' }})
                .then(res => res.data)
                .then(data => {
                    if(data.errMsg !== undefined)    alert(data.errMsg);
                    else{
                        alert(data.message);
                        this.setState({friendId:""});
                        this.fnSearch();
                    }
                })
                .catch(e => { alert(e); })
            }
        }

        // Func: 친구 삭제
        const fnDelFriend = item => {
            const { userName, friendId } = item;
            if(window.confirm("[" + userName + "]님을 친구목록에서 삭제하시겠습니까?")){
                instance.post("/api/friend/del", { friendId }, {headers: { 'Content-Type': 'application/json;charset=UTF-8' }})
                .then(res => res.data)
                .then(data => {
                    if(data.errMsg !== undefined)    alert(data.errMsg);
                    else{
                        this.fnSearch();
                    }
                })
                .catch(e => { alert(e); })
            }
        }

        return (
            <div id="friend_wrap">
                <div id="friend_header">
                    <h4>MapMarker</h4>
                    <Link to="/main" className="btn_home">공유지도</Link>
                    <button onClick={e => window.history.back()} className="btn_back">뒤로가기</button>
                </div>{ /* #friend_header */ }
                <div id="friend_content">
                    <div className="friend_form">
                        <input
                            type        = "text"
                            value       = {this.state.friendId??""}
                            placeholder = "친구 이름, 이메일 검색"
                            onKeyUp     = {fnShowList}
                            onChange    = {e=>{ this.setState({friendId:e.target.value})}}
                        />
                        <button className="btn_add" onClick={e => fnAddFriend()}>+</button>
                    </div>
                    <div className="friend_list">
                        {
                            (this.state.list && this.state.list.length > 0)?
                            this.state.list.map((item, index) => {
                                const friendId = this.state.friendId.trim();
                                if(    friendId === ""
                                    || item.userName.indexOf(friendId) >= 0
                                    || item.friendId.indexOf(friendId) >= 0
                                ){
                                    return (
                                        <dl key={"friend" + index}>
                                            <dt className="photo"><img src={item.profile?(IMAGE_SERVER + item.profile):"/images/app/ic_launcher.png"}/></dt>
                                            <dd className="title">
                                                <strong className="name">{item.userName}</strong>
                                                <span className="email">{item.friendId}</span>
                                            </dd>
                                            <dd className="control"><button className="btn_del" onClick={() => { fnDelFriend(item)}}>x</button></dd>
                                        </dl>
                                    )
                                }else{
                                    return <></>;
                                }
                            })
                            :null
                        }
                    </div>
                </div>
            </div>
        )
    }
}